import React, { useState, useEffect } from 'react';
import { Box, Grid } from '@mui/material';

import { Stl } from '../../Stl';
import { MediaQuery } from '../../MediaQuery' ;
import { getLastHour } from '../helpers/getLastHour';
import { unitToTwoUnit } from '../helpers/unitToTwoUnit';

import { Square } from './Square' ;
import { PlotFlowRespiratorio } from './PlotFlowRespiratorio' ;

import { BarraPlot } from './BarraPlot';
import { getData } from '../respiratorio/getData' ;
import { getLabels } from '../respiratorio/getLabels' ;
import { Title }  from '../elements/Title';
import { Info } from './Info' ;
import { Catalogo } from './Catalogo' ;
import { TimeLine } from './TimeLine' ;

import { Block } from './Block';

const Squares = ({args}) => {

     const styleSquare = {
          marginBottom: '0.1rem',
     };

     const { data,  dayNow, id } = args ;

     const [ labels, setLabels ] = useState([]);

     const [ select, setSelect ] = useState([
          "FIO2",
          "respir_rate",
          'mean_press',
          "PEEP",
          'peakP',
          'tidal_volume_set_334',
          'power',
          'inspTV'
     ]);

     useEffect( ()=>{
          getLabels(dayNow, id, setLabels) ;
     },[dayNow, id])

     return(
          <>
               {
                    select.map( (label, idx) =>{

                         const obj = data.find( obj => obj.name === label) ;

                         return(
                              <Grid
                                   item
                                   xs={6} xl={6}
                                   align='center'
                                   key={idx}
                              >
                                   <div
                                        style={styleSquare}
                                   >
                                        {
                                             obj !== undefined &&
                                                  <Square
                                                       day={dayNow}
                                                       idPat={id}
                                                       id={idx}
                                                       color_index={idx}
                                                       title={labels.find( item => item[0] === label)}
                                                       tendencia={obj?.data[0] }
                                                       prom={obj?.data[1] }
                                                       value={obj?.data[2] }
                                                       array={obj.data[3].map( array => [ !parseFloat(array[0]) ? parseFloat(obj?.data[1]) : parseFloat(array[0]) ,array[1]] )}
                                                  />
                                        }
                                   </div>
                              </Grid>
                         )
                    })
               }
               <Grid item xs={12} >
                    <Catalogo
                         data={ data }
                         select={select}
                         setSelect={setSelect}
                         labels={ labels.filter( label =>
                              [
                                   'diagnosis', 'age','height',
                                   'weight', 'gender','PBW',
                                   'BMI','assessment','comment_out','mode'
                              ].map( item => item !== label[0] ).findIndex( flag => flag === false ) < 0  )
                         }
                    />
               </Grid>
          </>
     )
}

const Plots = ({data}) => {

     const stylePlots = {
          marginBottom: '0.3rem',
          border: '2px solid hsl(242, 0%, 35%)',
          borderRadius: '0.2rem',
          paddingTop: '0.2rem',
     };

     // Parametro de las graficas
     const [point, setPoint] =useState({value:80}) ;
     const [len, setLen] = useState(200) ;

     return(

          <Grid
               container item
               direction="row"
               xs={8} xl={8}
               spacing={1}
               columns={12}
          >

               <Grid
                    item  container align='center' xs={12}
                    spacing={1}
               >

                    {
                         data.length > 0 &&

                         [    ["flow_exp_amplitudes", "FlowExp", '#006350'],
                              ["flow_signal", "FlowSignal",  '#09AD84'],
                              ["press_signal", "PressSignal", '#009196'],
                              ["press_amplitudes", "PressAmp", '#097CAD'],
                              ["flow_insp_amplitudes", "FlowAmp" , '#04224A']
                         ].map( (label, idx) =>{

                              const obj = data.find( obj => obj.name === label[0] );

                              if( obj !== undefined ){
                                   return(
                                        <Grid
                                             item  align='center'
                                             key={idx}
                                             xs={6}
                                        >
                                             <div style={stylePlots} >
                                                  <PlotFlowRespiratorio
                                                       data={ obj }
                                                       name={ label[1] }
                                                       point={ point.value }
                                                       setPoint={setPoint}
                                                       len={len}
                                                       setLen={setLen}
                                                       color={ label[2] }
                                                  />
                                             </div>
                                        </Grid>
                                   )
                              }
                              return(
                                   <span></span>
                              )
                         })
                    }
               </Grid>

               <Grid
                    item  align='center' xs={12}
               >
                    {
                         data.length > 0 &&
                         <BarraPlot set={setPoint} point={point.value}/>
                    }
               </Grid>
          </Grid>

     )
}

const dta_demo = (data) => {

     const dta = {
          title1:'Inf. Demogrfica',
          title2:'Informacin Demogrfica',
          body:[]
     };
     [
          ['PBW', 'PWM',''],
          ['BMI', 'BMI',''],
          ['age', 'Edad','Aos'],
          ['weight', 'Peso','Kg'],
          ['height', 'Altura','cm'],
     ].forEach( label => {
          const value = data.find( item => item.name === label[0] )
               if(value){
                    dta.body.push(
                         {
                              name:`${label[1]}:`,
                              data:`${value.data[2]} ${label[2]}`
                         }
                    )
               };
     });
     return dta ;
};

const dta_eval = (data) => {

     const dta = {
          title1:'Evaluaciones',
          title2:'Evaluaciones',
          body:[]
     };
     [
          ['assessment', 'Evaluacin',''],
     ].forEach( label => {
          const value = data.find( item => item.name === label[0] )
          if(value){
               value.data[3].find( array => array[0].length >= 1 )[0]
                    .filter( item => item.length > 0)
                    .forEach( item => {
                         dta.body.push(
                              {
                                   name:`${label[1]}:`,
                                   data: item.split('.')[1]
                              }
                         )
                    });
          };
     });
     return dta ;
}

const dta_mode = (data) => {

     const dta = {
          title1:'Modos',
          title2:'Modos',
          body:[]
     };
     [
          ['mode', 'Modo',''],
     ].forEach( label => {
          const value = data.find( item => item.name === label[0] )
          if( value ){ 
               if( value.data[3][0][0] !== 'NA' ){
                    value.data[3]
                         .find( array => array[0].length >= 1 )[0]
                         .split(' ')[1]
                         .split('/').forEach( mode => {
                              dta.body.push(
                                   {
                                        name:`${label[1]}:`,
                                        data: mode
                                   }
                              )
                         })
               }else{
                    dta.body.push(
                         {
                              name:`${label[1]}:`,
                              data: 'NA'
                         }
                    )
               }
          };
     });
     return dta ;
}




export const Respiratorio = ({dt}) => {

     const classes = Stl();
     const { _xl, _lg } = MediaQuery() ;

     const [ id , dayNow  ] = dt ; // fecha establecida por el TimeLine: [ dia, mes, ao ]
     const [ hour, setHour ] = useState( getLastHour( 'lastHour' ) ) ;
     const [ data, setData ] = useState([]) ;
     const [ msn, setMsn ] = useState('Recuperando informacin') ;

     useEffect( () => {
          setMsn('Recuperando informacin') ;
          getData(dayNow, hour, id, setData, setMsn) ;
     },[dayNow, id, setData, setMsn, hour]) ;

     const hourRegistro = () => {
          const date = new Date(`${dayNow[2]}-${unitToTwoUnit(dayNow[1])}-${unitToTwoUnit(dayNow[0])}${hour}`);
          return(  new Date(date.getTime() + 1000*60*60).toLocaleString() );
     }

     const sinDatos = () => {
          return(
               <Title>
                    {msn}
               </Title>
          )
     };


     return(
          <Box
               className={[classes.px, classes.bg_white, classes.my_1, classes.py].join(' ')}
               // onDragEnd={ e => handleDragEnd(e) }
               // onD, day, id, labelsrag={ e =>{ console.log("OnDrag:",e.movementX, e.movementY)}}
               // onDragStart={e => handleDragtStart(e) }
               style={{height: _xl?'950px': _lg?'805px':'708px',overflowY: 'scroll', scrollBehavior: 'smooth'}}

          >
               <Grid
                    container
                    direction="row"
                    justifyContent="flex-start"
                    alignItems="flex-start"
                    spacing={1}
               >
                    {
                         [
                              dta_demo,
                              dta_eval,
                              dta_mode
                         ].map( (f,i) => <Block key={i} data={ f(data) }/>)
                    }

                    <Grid
                         item  container
                         align='center'
                         xs={12}
                    >
                         <TimeLine setHour={setHour} dayNow={dayNow} />
                    </Grid>

                    <Grid
                         item  container
                         align='center'
                         xs={6}
                    >
                         <div
                              style={{paddingBlock: '0.2rem', fontSize:'1.2rem', fontWeight: 'bold'}}
                         >
                              {`Hora del registro: ${hourRegistro().split(', ')[1]}`}
                         </div>
                    </Grid>

                    <Plots data={data}/>

                    <Grid
                         container item
                         direction="row"
                         xs={4} xl={4}
                         spacing={1}
                    >

                         <Squares args={{data,  dayNow, id }}/>

                         <Grid item xs={12} >
                              <Info/>
                         </Grid>

                    </Grid>
                    {
                         !(data.length > 0) &&
                         sinDatos()
                    }
               </Grid>
          </Box>
     );

}
