import React, { useState } from 'react';
import { Box } from '@mui/material';
import {PlotTimeLine} from './PlotTimeLine' ;

import { Stl } from '../../Stl';

import { Title } from '../elements/Title' ;
import { Field } from '../elements/Field' ;

//redux
import { useDispatch } from 'react-redux' ;
import { assignDay } from '../../../../redux/dayNowSlice' ;
import { unitToTwoUnit } from '../helpers/unitToTwoUnit';


export const TimeLine =  () =>{

     const classes = Stl() ;
     const dispatch = useDispatch();

     // const [date,SetData] = StateTimeLine();
     // Fecha Actual
     const dayNow = new Date();
     const day =  unitToTwoUnit( dayNow.getUTCDate() );
     const month =  unitToTwoUnit( dayNow.getUTCMonth()+1 );
     const year = dayNow.getUTCFullYear();

     const [ date, SetData ] = useState({
          fecha: [day, month, year]
     });

     dispatch( assignDay(date.fecha) );

     const handleHoy =()=>{
          SetData({fecha: [day,month,year]});
     };

     const title = (value) => {

     const styleTitle = [classes.bg_blue].join(' ');

          return(
               <Box className={[styleTitle, classes.just_content_spa_bet].join(' ')}>
                    <Title color={'white'}>
                         {value}
                    </Title>
                    <Box
                         py={0.5}
                         px={0.5}
                         mr={1}
                         onClick={()=> handleHoy()}
                         className={[classes.bg_white, classes.br_03, classes.button, classes.row].join(' ')}
                    >
                         <Field color={'blue'}>
                              Hoy
                         </Field>
                    </Box>
               </Box>
          )
     };

          return(
               {
                    timeLine:()=>{
                         return(
                              <Box my={1} className={ classes.bg_white }>

                                   {title('Linea de tiempo')}

                                   <PlotTimeLine DateNow={date.fecha} set={SetData}/>

                              </Box>
                         )
                    },
                    dayNow: date.fecha
               }
          );

    }
