
const timeEvent = ( string ) => {
     const [t, i] = string.split('-') ;
     return [ t.replace(/[ .]/g,''), i] ;
};

export const orderJornada = ( data, objs ) => {

     data.sinAsignar = [];
     const labels =  Object.keys( objs )  ;

     labels.forEach( (label,idx) => { 

          if( objs[label][0] ){//objs[label].length > 0 ){ 

               if( objs[label][0].value !== '--' ){
                    const [time, interval] = timeEvent( objs[label][0].value )

                    if( (time.search('a')>-1 && time.split(':')[0] >= 6) || (time.search('p')>-1 && time.split(':')[0] < 6) ){ // dia
                         data.dia.push({
                              label : label,
                              time: time,
                              interval: interval
                         })
                    }else{
                         data.noche.push({
                              label : label,
                              time: time,
                              interval: interval
                         })
                    }
               }else{
                    data.sinAsignar.push({
                         label : label
                    })
               };
          };
     });
};