
import React, {useState} from 'react' ;
import { styled } from '@mui/system';
import { MediaQuery } from '../../MediaQuery' ;
import { SubTitle } from '../elements/SubTitle' ;
import { prom } from '../dashboard/helpers/prom'

export const Catalogo = ( {data, select , setSelect, labels} ) =>{

     const {_xl, _lg} = MediaQuery() ;

     const [displayCuadro, setDisplay] = useState({
          state:'none'
     });

     const Cuadro = styled('div')({
          boxShadow: 'rgba(0, 0, 0, 0.4) 5px 5px 5px 3px',//boxShadow: 'rgb(80,80,255,40%) 4px 4px',
          display:displayCuadro.state,
          border:'1px solid rgb(100,100,100,60%)',
          borderRadius:'3px',
          fontFamily:'Montserrat',
          paddingLeft:  '0.8rem',
          paddingTop:'0.3rem',
          color:'rgb(70,70,70)',
          position: 'absolute',
          top: _xl ? '15%' :_lg ? '35%' : '12%',
          left: _xl ? '27.5%' :_lg ? '28.5%' : '29%',
          width: _xl ? '1350px' : _lg ? '1035px':'800px',
          background:'rgb(255,255,255,95%)',
          '&:hover':{
               zIndex:'1000',
          }
     })

     const StyleCat = styled('div')({
          fontSize: _xl ? '25px' : _lg ? '20px': '14px',
          fontFamily:'Montserrat',
          border:'2px solid rgb(00,80,255,80%)',
          background: 'rgb(00,80,255,30%)',
          borderRadius:'3px',
          cursor:'pointer',
          color:'rgb(70,70,70)',
          textAlign:'center',
          paddingBlock:'0.2rem',
          '&:hover':{
               border:'2px solid #0d50a1',
               background: 'rgb(00,80,255,10%)'
          }
     });

     const Item = styled('div')({
          width:'25%',
          display:'flex',
          alignItems: 'center',
          marginBlock:'0.1rem',
     });

     const Input = styled('input')({
          cursor:'pointer',
          transform:`scale(${_xl ? 1.8 : _lg ? 1.8 : 1})`,
          height:_xl ? '1.5rem' : _lg ? '1.5rem':'1rem',
     });

     const Label = (props) => {

          const ObjRender = styled('label')({
               padding:'0.12rem',
               fontSize: _xl ? '25px' : _lg ? '20px': '14px',
               marginLeft:'0.5rem',
               cursor:'pointer',
               border: `2px solid ${props.color ? 'rgb(199, 143, 137)' : '#fff'}`,
               background: props.color ? '#fef2e1' : '#fff',
               borderRadius:'0.2rem',
               '&:hover':{
                    border:'2px solid #0d50a1',
                    background: 'rgb(00,80,255,10%)'
               }
          });

          return(
               <ObjRender htmlFor={props.htmlFor}> {props.children} </ObjRender>
          )
     };

     const Svg = styled('svg')({
          borderRadius:'3px',
          marginRight:'0.2rem',
          cursor:'pointer',
          '&:hover':{
               background: 'rgb(00,80,255,30%)'
          }
     });

     const defColor = (label) => {

          if( data.length === 0 ) return(0);

          let obj = data.find( obj => obj.name === label[0] ) ;
          const low = parseFloat(label[4].split('-')[0]) ;
          const hight = parseFloat(label[4].split('-')[1])

          const mean = prom( obj.data[3].map(n => n[0]) );

          return (
               obj.data[3].findIndex( num => ( num[0] === 0 ? mean : num[0] ) <= low ) >= 0
               ||
               obj.data[3].findIndex( num => ( num[0] === 0 ? mean : num[0] ) >= hight ) >= 0
          );
     };

     const handleClick = (value) => {
          if( displayCuadro.state === 'none'){
               setDisplay({state:'block'})
          }else{
               setDisplay({state:'none'})
          };
     };

     const handleSelect = (e) => {

          localStorage.removeItem(`intervalos`);

          if( select.findIndex( item => item === e.target.value ) >= 0 ){
               setSelect([
                    ...select.filter( item => item !== e.target.value )
               ])
          }else{
               setSelect( [
                    ...select, labels.find( item => item[0] === e.target.value )[0]
               ]);
          }

     };

     return(
          <>
               <StyleCat onClick={ ()=> handleClick() }>
                    <strong> Catlogo </strong>
               </StyleCat>

               <Cuadro>
                    <SubTitle>
                         Catlogo de variables
                         <span
                              onClick={ ()=> handleClick()}
                              align='rigth'
                              style={{paddingLeft:_xl ? 'calc(75.5%)' : _lg ? 'calc(75%)' : 'calc(76%)'}}
                         >
                              <Svg
                                   width={_xl ? '45' : '30'} height={_xl ? '45' : '30'}
                                   viewBox="0 0 30 30" fill="none" xmlns="http://www.w3.org/2000/svg"
                              >
                                   <path
                                        d='M26.3 0.8H9.3C7.7 0.8 6.4 2.1 6.4 3.6V20.6C6.4 22.2 7.7 23.4 9.3 23.4H26.3C27.9 23.4 29.1 22.2 29.1 20.6V3.6C29.1 2.1 27.9 0.8 26.3 0.8ZM26.3 20.6H9.3V3.6H26.3V20.6ZM3.6 6.4V26.3H23.4V29.1H3.6C2.1 29.1 0.8 27.9 0.8 26.3V6.4H3.6ZM11.8 16.1L15.8 12.1L11.8 8.1L13.8 6.1L17.8 10.1L21.8 6.1L23.8 8.1L19.8 12.1L23.8 16.1L21.8 18.1L17.8 14.1L13.8 18.1L11.8 16.1Z'
                                        fill="black"
                                        fillOpacity="0.65"
                                   />
                              </Svg>
                         </span>
                    </SubTitle>

                    <div
                         style={{display:'flex', flexDirection:'row', flexWrap: 'wrap', marginBlock:'1rem'}}
                    >
                         {
                              labels.map( (label,idx) =>
                                   <Item
                                        key={idx}
                                   >
                                        <Input
                                             id={`${idx}label[0]`}
                                             type='radio'
                                             value={label[0]}
                                             checked={ select.findIndex( item => item === label[0] ) >= 0 ? true :  false}
                                             onChange={ (e) => handleSelect(e) }
                                             onClick={ (e) => handleSelect(e) }
                                        />
                                        <Label
                                             htmlFor={`${idx}label[0]`}
                                             color={ defColor(label)}
                                        >
                                             {label[1]}
                                        </Label>
                                   </Item>
                              )
                         }
                    </div>
               </Cuadro>
          </>
     )
};
