import React, {useEffect } from 'react';
import * as d3 from "d3";

import { MediaQuery } from '../../MediaQuery';


const margin = { right:13, top:10,  bottom: 35, left: 17, } ;

// funcion que gobierna el svg:
const plot = (svgSindro, interval, normal, setNormal, _xl, _lg) => {

     const fontSize = _xl ? '22px' : _lg ? '16px': '12px' ;

     let flag = false ;
     const ajustado = [
          normal[0] < interval[0],
          normal[1] > interval[1]
     ].map( (exp, idx) => {

          if( exp ){
               flag = true ;
               return  parseFloat((2*interval[idx] - normal[idx])).toFixed(1)  ;
          }
          return normal[idx]
     });

     if(flag){
          setNormal(ajustado.sort( (a,b)=>a-b))
     };



     const svgEl = d3.select(svgSindro.current);
     svgEl.selectAll("*").remove(); // Clear svg content before adding new elements

     const width = svgEl._groups[0][0].clientWidth  ;
     const height = svgEl._groups[0][0].clientHeight  ;

     const svg = svgEl.append("g")
                    .attr("width", `${width }`)
                    .attr("height",`${height}`)

     // Parametros del objeto
     const xScale = d3.scaleLinear()
                    .domain( interval )
                    .range( [margin.right, width-margin.left] );

     // line
     svg.append('line')
          .attr('x1', xScale(interval[0]) )
          .attr('x2', xScale(interval[1]) )
          .attr('y1', 2*height/4 )
          .attr('y2', 2*height/4 )
          .attr('stroke', '#525252')
          .attr('stroke-width','0.2rem') ;

     // circulos
     [
          interval[0],
          interval[1]
     ].forEach( (e) =>{
               svg.append('circle')
                    .attr('cx', xScale(e) )
                    .attr('cy', 2*height/4 )
                    .attr('r', 5)
                    .attr('fill','#525252')
     }) ;

     // barras rojas
     [
          [xScale(interval[0]) + 7, xScale(normal[0]), 'rgba(166, 0, 0, 0.83)'],
          [xScale(normal[1]), xScale(interval[1]) - 7, 'rgba(166, 0, 0, 0.83)'],
          [xScale(normal[0]), xScale(normal[1]) , 'rgba(0, 157, 35, 0.83)']
     ].forEach( item => {
          svg.append('line')
               .attr('x1', item[0] )
               .attr('x2', item[1] )
               .attr('y1', 2*height/4  )
               .attr('y2', 2*height/4  )
               .attr('stroke', item[2])
               .attr('stroke-width',_lg ? '2rem' : '1.5rem')
     });

     // marcas de texto en extremos
     [
          xScale(interval[0])-13,
          xScale(interval[1])-6
     ].forEach( (item,idx) => {
          svg.append('text')
               .attr('x', item )
               .attr('y', 2*height/4 + 17 )
               .attr('stroke', '#525252')
               .attr("font-family", "Montserrat")
               .attr('font-weight', '300')
               .attr('fontSize', fontSize )
               .text(interval[idx]);
     });

     // indicadores de marca
     let dx = 0 ;
     const dragHandler = {} ;
     [
          [xScale(normal[0])-13, _lg ? 2*height/4-16 : 2*height/4-13],
          [xScale(normal[1])-13, _lg ? 2*height/4-16 : 2*height/4-13]
     ].forEach( (item, i) => {
          svg.append('path')
               .attr('id', `arrow_${i}`)
               .attr( 'd' , 'M12.5 18L0.808656 0.75L24.1913 0.750002L12.5 18Z')
               .attr('fill', '#030000')
               .attr('transform',`translate( ${ item } )` )
               .attr('style','cursor:pointer')

          dragHandler[`handler_${i}`] = d3.drag()
               .on('drag',(event) => {

                    if( event.dx < 50 ){
                         dx = dx + event.dx*(interval[1]-interval[0])/(_xl?700:_lg?500:400) ;

                         let value = dx + parseFloat(normal[i]) ;
                         
                         if( value  > (interval[0]+0.1) & value  < (interval[1]-0.1) ){

                              setNormal(
                                   [ parseFloat(value).toFixed(2), parseFloat(normal[1-i]).toFixed(2) ].sort((a,b)=> a-b)
                              );
                         }
                    }
               });

          dragHandler[`handler_${i}`]( svg.select(`#arrow_${i}`) );
     });

};

export const Barra = ({interval, setNormal, normal}) => {

     const svgSindro = React.useRef(null);
     const {_xl, _lg} = MediaQuery() ;

     useEffect( ()=> {
          plot(svgSindro, interval, normal, setNormal,_xl, _lg);
     },[svgSindro, interval, normal, setNormal,_xl, _lg]);

     return(
          <svg ref={svgSindro} width={'100%'} height={'40px'} />
     );
};
