

import { styled } from '@mui/system';
import { Text } from '../../elements/Text';


export const CheckBox = (props) => {

    const Box = styled('div')({
        paddingBlock: '0.5rem',
        display: 'flex',
        flexWrap: 'wrap'
    });

    const styleInput = {
        width:'45%',
        margin:'0.2rem',
    };


    return(
        <Box>
            {
                Object.keys(props.labels).map( (label,idx) => {
                    return(
                        <div key={idx} style={styleInput}> 
                            <input 
                                type="checkbox" 
                                id={label} 
                                checked={ props.labels[label] }
                                style={{ transform:'scale(1.4)', cursor:'pointer'}}
                                onChange={ e => props.set({ ...props.labels, [label] : e.target.checked }) }
                            />
                            <label htmlFor={label}>
                                <Text element={false} style={{marginLeft:'0.5rem', cursor:'pointer'}}>
                                    {label} 
                                </Text>
                            </label>
                        </div>
                    )
                })
            }
        </Box>
    )
}
