import React, { useState, } from 'react';
import { Box, Grid } from '@mui/material';
import Link from '@mui/material/Link'
import { styled } from '@mui/system';

// Componentes:
import { Title } from '../elements/Title';
import { SubTitle } from '../elements/SubTitle';
import { Text } from '../elements/Text';
import { Square } from './components/Square';
import { CheckBox } from './components/CheckBox';

// helpers
import { unitToTwoUnit } from '../helpers/unitToTwoUnit';
import { externalPage, resourceServer_backend } from '../../env';
import { addToken }  from '../helpers/token';



export const HistoriaTab = ({ dt }) => {

     const { urlPage } = externalPage ;

     const { postServer, urlServer } = resourceServer_backend ;
     
     const styleTitle = {
          background: '#0d47a1'
     }

     const styleBoxContainer = {
          background: 'white',
          padding: '0.5rem'
     }

     const ButtonPage = styled('button')({
          paddingBlock: '0.5rem',
          paddingInline: '0.8rem',
          background: '#A7DDFF',
          border: '1px solid #005BA5',
          borderRadius: '5px',
          '&:hover': {
               background: 'white',
          },
     });

     const { date } = JSON.parse( localStorage.getItem('Admision') );

     const [periodo, setPeriodo] = useState({
          startDate: date,
          endDate: new Date().toLocaleString()
                              .split(',')[0].split('/')
                              .reverse().map( num => unitToTwoUnit(num) ).join('-')
     });

     const [dashFlags, setDashFlags] = useState({
          ...['Pres. Arterial', 'Pres. Cardiaca', 'Temp. Corportal', 'Frec. Respiratoria']
               .reduce( (acc, cur) => {
                    acc[ cur ] = true  ;
                    return acc 
               }, {})
     });

     const [balFlags, setBalFlags] = useState({
          ...['Ingresos', 'Egresos']
               .reduce( (acc, cur) => {
                    acc[ cur ] = true  ;
                    return acc 
               }, {})
     });

     const [respFlags, setRespFlags] = useState({
          ...[
               'FIO2', 'Head', 'Power', 'Mean Press', 'Evaluaciones', 'Peak Press', 
               'Plateau Press', 'Modes', 'FR', 'Signal Press', 'Signal Flow', 
          ].reduce( (acc, cur) => {
               acc[ cur ] = true  ;
               return acc 
          }, {})
     });

     const [formFlags, setFormFlags] = useState({
          ...[
               'Ind. Mdicas', 'Ctr. Hemodinmico', 'Ingresos', 'Bal. Hdrico',
               'Exam. fsico', 'Observaciones', 'Programacin'
          ]
          .reduce( (acc, cur) => {
               acc[ cur ] = true  ;
               return acc 
          }, {})
     });
     

     const handleClick = () => {
          
          const idPatient = JSON.parse( localStorage.getItem('idPatient') );

          const objData = {
               ...idPatient,
               periodo,
               dashFlags,
               balFlags,
               respFlags,
               formFlags,
          };

          postServer.body = JSON.stringify( objData );
          fetch(`${urlServer}?query=pagePrinted`, addToken(postServer, "application/json" ))
               .catch( err => console.log( "Error en envio de parametros para impresion",err));
     };


     return (
          <>
               <Box style={styleTitle}>
                    <Title color={'white'} >
                         Versin de impresin de  la historia clnica
                    </Title>
               </Box>
               
               <Box style={styleBoxContainer}>

                    <Grid
                         container
                         direction="row"
                         justifyContent="space-between"
                         alignItems="center"
                         mb={1}
                         spacing={1}
                    >
                         <Grid item xs={9}>
                              <SubTitle >
                                   Seleccin de parmetros  para la impresin de la histora clnica:
                              </SubTitle>

                         </Grid>
                         <Grid
                              item xs={3}
                              style={{ textAlign: 'end' }}
                         >
                              <ButtonPage onClick={ ()=>handleClick() }>
                                   <Link
                                        target="_blank"
                                        href={urlPage}
                                        underline="hover"
                                        color="inherit"
                                   >
                                        <Text>
                                             Versin de Impresin
                                        </Text>
                                   </Link>
                              </ButtonPage>

                         </Grid>
                    </Grid>


                    <Grid
                         container
                         spacing={1}
                         direction="row"
                         justifyContent="space-between"
                         alignItems="stretch"
                    >

                         <Grid item xs={5}>

                              <Square title='Perodo'>

                                   Desde:
                                   <input
                                        type='date'
                                        min={date.replace(/[ ]/g, "").split(':')[0]}  // "2018-01-01"
                                        max={`${periodo.endDate}`}    // "2018-01-01"
                                        value={`${periodo.startDate}`}
                                        onChange={(e) => setPeriodo({ ...periodo, startDate: e.target.value })}
                                   />

                                   <span style={{ marginLeft: '0.5rem' }}>
                                        Hasta:
                                        <input
                                             type='date'
                                             min={date.replace(/[ ]/g, "").split(':')[0]}
                                             max={`${periodo.endDate}`}
                                             value={`${periodo.endDate}`}
                                             onChange={(e) => setPeriodo({ ...periodo, endDate: e.target.value })}
                                        />
                                   </span>
                              </Square>


                              <Square title='Balance Hdrico'>
                                   <CheckBox labels={balFlags} set={setBalFlags} />
                              </Square>

                              <Square title='Formulario'>
                                   <CheckBox labels={formFlags} set={setFormFlags} />
                              </Square>

                         </Grid>
                         <Grid item xs={7}>

                              <Square title='Respiratorio'>
                                   <CheckBox labels={respFlags} set={setRespFlags} />
                              </Square>

                              <Square title='Dashboard'>
                                   <CheckBox labels={dashFlags} set={setDashFlags} />
                              </Square>
                              

                         </Grid>

                    </Grid>

               </Box>
          </>

     );

};
