import { unitToTwoUnit } from '../helpers/unitToTwoUnit';
import { getLastHour } from '../helpers/getLastHour';
import { resourceServer } from '../../env'


export const makeAlerts = (dayNow, id, label, intervalos) => {

     const dte = `${dayNow[2]}-${unitToTwoUnit(dayNow[1])}-${unitToTwoUnit(dayNow[0])}` ;// 2022-08-10
     const hour =  getLastHour( 'lastHour' ) ;
     const { urlServer, requestOptionsGet, requestOptionsPut } = resourceServer ;


     fetch(`${urlServer}Observation?identifier=${dte}${hour}&identifier=metricas&subject=${id}`,
          requestOptionsGet).then( resp => {
               resp.text().then( obj => {

                    const data = JSON.parse( obj )

                    if( data?.total > 0 ){

                         const url = data.entry[0].fullUrl.split('v4/')[1],  resource = data.entry[0].resource ;

                         let obj = resource.component.find( comp => comp.code.text === label ) ;

                         if( obj ){

                              let objIdx = resource.component.findIndex( comp => comp.code.text === label ) ;
                              // let flag = obj.code?.coding

                              obj.code.coding = [
                                   { display: obj.code.coding[0].display  },
                                   { display: obj.code.coding[1].display  },
                                   { display: obj.code.coding[2].display  },
                                   { display: `VarInterval:${intervalos[0][0]}-${intervalos[0][1]}`  }, // variable interval
                                   { display: `NorInterval:${intervalos[1][0]}-${intervalos[1][1]}`  }  // normal interval
                              ];

                              resource.component[objIdx] = obj;
                         };

                         requestOptionsPut.body = JSON.stringify(resource);

                         fetch(`${urlServer}${url}`,requestOptionsPut)

                    };

               })
          })
};
