

import { resourceServer } from '../../../env' ;
import { addToken } from '../../helpers/token'; 

const { urlServer, requestOptionsGet } = resourceServer ;

export const getMedicacion = async( id, dayNow, setMedicacion ) => {

     const response = await fetch(`${urlServer}MedicationDispense?subject=${id}`, addToken(requestOptionsGet) );
     let { entry, total, link } = await response.json();

     // Ms de una pagina de respuesta
     if( link.length > 1 ){
          const resp = await fetch(`${urlServer}MedicationDispense?_count=${total}&subject=${id}`, addToken(requestOptionsGet) )
          const json = await resp.json();
          entry = json.entry ;
     };

     if( total > 0 ){
          const medicamentos = entry.map( ( item )=>{
               let obj = {};
               obj.categoria = item.resource.category.text ;
               obj.medicamento = item.resource.medicationReference.display ;
               obj.intervaloValue = item.resource.daysSupply.value ;
               obj.intervaloUnit = item.resource.daysSupply.unit ;
               obj.date = new Date( item.resource.whenHandedOver ) ;
               obj.dosisValue = item.resource.quantity.value ;
               obj.dosisUnit = item.resource.quantity.unit ;
               return obj ;
          });
          setMedicacion( [...medicamentos] ) ;
     };
};
