import React,{useEffect} from "react";
import * as d3 from "d3";
import { MediaQuery } from '../../MediaQuery';

import { meanArray } from './meanArray' ;

const Plot = ({ data, svgRef, _xl, _lg } ) => {

     const color_fill = 'rgba(0, 91, 165, 1)';
     const date =  (hour) => new Date( new Date().toLocaleString().split(',')[0].split('/').reverse().join('-') + hour ) ;

     const points = data.array
                         .sort( (a,b) => a[1].split(':')[0] - b[1].split(':')[0])
                         .map( array => [
                              array[0],
                              date( ` ${array[1]}:00` ),
                              array[1]
                         ]);

     const prom =   meanArray(data.array)

     const margin = {
          top:11,
          right:_xl?35:_lg?25:20 ,
          bottom:_xl?24:_lg?21:20,
          left:_lg?30:12,
          m:_xl?20:_lg?20:20,
          offSetY:_lg?30:18
     } ;

     const fontSize = _xl ? '14px' : _lg ? '11px': '9px';

     const svgEl = d3.select(svgRef.current);
     svgEl.selectAll("*").remove(); // Clear svg content before adding new elements

     const width = svgEl._groups[0][0].clientWidth  ;
     const height = svgEl._groups[0][0].clientHeight  ;


     const svg = svgEl.append("g")
                    .attr("width", `${width}`)
                    .attr("height",`${height}`)

     const  xScale = d3.scaleTime()
                         .domain(
                              [
                                   date( ' 00:00:00' ) ,
                                   date( ' 23:59:00' )
                              ]
                         )
                         .range([ margin.left + margin.m , width-margin.right ])
                         .nice() ;

     // Insercion del eje x Dia
          svg.append("g")
               .attr("transform", `translate(0, ${height-margin.bottom})`)
               .call( d3.axisBottom(xScale).ticks(6) )
               .attr("font-size", fontSize)
               .attr("color", color_fill )
               .attr('opacity','0.85')
               .selectAll("text")
               .attr("transform","rotate(17,0,15)")
               .attr('style', `font-size:${fontSize}; font-family: Montserrat !important`) ;

     // const  xScaleDia = d3.scaleTime()
     //                     .domain(
     //                          [
     //                               new Date( new Date().toLocaleString().split(',')[0].split('/').reverse().join('-')+' 06:00:00' ) ,
     //                               new Date( new Date().toLocaleString().split(',')[0].split('/').reverse().join('-')+' 17:59:00' )
     //                          ]
     //                     )
     //                     .range([margin.left + margin.m , 3*width/4])
     //                     .nice() ;
     //
     // const  xScaleNoche = d3.scaleTime()
     //                     .domain(
     //                          [
     //                               new Date( new Date().toLocaleString().split(',')[0].split('/').reverse().join('-')+' 18:00:00' ) ,
     //                               new Date( new Date().toLocaleString().split(',')[0].split('/').reverse().join('-')+' 23:59:00' )
     //                          ]
     //                     ).range([3*width/4, width-margin.right])
     //                     .nice() ;

     // Insercion del eje x Dia
     // [
     //      d3.axisBottom(xScaleDia).ticks(6), // Insercion del eje xDia
     //      d3.axisBottom(xScaleNoche).ticks(3)  // Insercion del eje xNoche
     // ].map( scale =>
     //      svg.append("g")
     //           .attr("transform", `translate(0, ${height-margin.bottom})`)
     //           .call( scale )
     //           .attr("font-size", fontSize)
     //           .attr("color", color_fill )
     //           .attr('opacity','0.85')
     //           .selectAll("text")
     //           .attr("transform","rotate(17,0,15)")
     //           .attr('style', `font-size:${fontSize}; font-family: Montserrat !important`)
     //           .attr('id', (d,i)=> `id_${new Date(d).toLocaleString()
     //                                         .split(' ')[1]
     //                                         .split(':')[0]}-${i}`
     //           )
     // );
     // d3.select('#id_12-6').remove() // Borrar la ultima marca del xScaleDia

     const yScale = d3.scaleLinear()
          .domain( d3.extent(points, (d) => ( d[0] ) )
                    .reverse()
                    .map( (n,i) => i===0 ? n+prom/10 : n-prom/10 )
          )
          .range([margin.top , height-margin.bottom]) ;

     // objeto del eje y
     const yAxis = d3.axisLeft(yScale)
          .tickSizeOuter(0)
          .ticks(4  ,"a")
          .tickSizeInner( -width+margin.right+margin.m+margin.left )

     // eje y
     svg.append("g")
          .attr("transform", `translate(${margin.left+margin.m} ,0)`)
          .call(yAxis)
          .attr("font-family", "Montserrat")
          .attr("font-size", fontSize)
          .attr("color", color_fill )
          .call( g => g.selectAll(".tick line")
               .attr("stroke-opacity", 0.4)
               .attr("stroke-dasharray", "2,3"))


     // cuadrados de jornada
     svg.append('g')
          .selectAll('rect')
          .data( [
               [ date( ' 00:00:00' ), date( ' 06:00:00' ), '#CCCBF5' ],
               [ date( ' 06:00:00' ), date( ' 18:00:00' ), '#C0F5E1' ],
               [ date( ' 18:00:00' ), date( ' 23:59:00' ), '#CCCBF5' ],
          ])
          .enter()
          .append('rect')
          .attr('x' , d => xScale(d[0]) )
          .attr('y', margin.top )
          .attr('height', height - margin.bottom-8 )
          .attr('width', d => xScale(d[1])-xScale(d[0]) )
          .attr('fill', d => d[2] )
          .attr("opacity", 0.60 )

     // Datos
     // Linea
     svg.append("path")
          .datum( points )
          .attr("fill", "none")
          .attr("stroke", color_fill)
          .attr("stroke-width", 0.8 )
          .attr("d", d3.line()
               .x( (d,i) => xScale(d[1]) )
               //      d[2].split(':')[0] >= 6 && d[2].split(':')[0] <= 23 && i <= 8
               //           ? xScaleDia( d[1] )
               //           : xScaleNoche( d[1] )
               // )
               .y( d =>  yScale(d[0]) )
               )

     // circulos
     svg.selectAll("circle")
          .data( points )
          .enter()
          .append("circle")
          .attr("fill", color_fill)
          .attr("stroke", "none")
          .attr("cx", (d,i) =>  xScale(d[1]) )
               // d[2].split(':')[0] >= 6 && d[2].split(':')[0] <=23 && i <= 8
               //      ? xScaleDia( d[1] )
               //      : xScaleNoche( d[1] ) )
          .attr("cy", d => yScale(d[0]) )
          .attr("r", 3)


     // mouse event

     const focus = svg.append('g')
                    .append('circle')
                    .style("fill", "none")
                    .attr("stroke", color_fill)
                    .attr('r', 4.5)
                    .style("opacity", 0)

     const focusText = svg.append('g')
                         .append('text')
                         .style("opacity", 0)
                         .attr("text-anchor", "left")
                         .attr("alignment-baseline", "middle")
                         .attr("font-family", "Montserrat")
                         .attr("fill", color_fill )

     svg.append('rect')
          .style("fill", "none")
          .style("pointer-events", "all")
          .attr('x' , margin.left )
          .attr('y', margin.top )
          .attr('width', width - margin.right-margin.left )
          .attr('height', height - margin.bottom )
          .on('mousemove', (event)=> mousemove(event))
          .on('mouseover', () => {
               focus.style("opacity", 1)
               focusText.style("opacity",1) })
          .on('mouseout',() => {
               focus.style("opacity", 0)
               focusText.style("opacity", 0)
          });


     // const bisect = d3.bisector( d => d ).left ;
     const dateTime = points.map( d => d[1].getTime() ) ;

     const mousemove = (event) => {

          let x0 = xScale.invert( d3.pointer(event)[0] ) ;

          // let p = bisect( dateTime,  x0.getTime() );

          let p = dateTime.findIndex( arr => Math.abs(arr - x0.getTime())  < 950000  ) >= 0
                    ?  dateTime.findIndex( arr => Math.abs(arr - x0.getTime())  < 950000  )
                    :  12

          if( p >= points.length ) return ;

          focus.attr("cx", xScale( points[p][1] ))
               .attr("cy", yScale( points[p][0] ))

          focusText.text(`${points[p][0]} - ${points[p][1].toLocaleString().split(', ')[1].split(':').reduce( (acc,_,idx,arr) => acc = `${arr[0]}:${arr[1]}${arr[2].split(' ')[1].replace(/[. ]/g,'')}`)} `)
               .attr("x", margin.left +margin.m )
               .attr("y", margin.top-1)
     } ;

};

export const PlotGraficas =({data})=>{

     const svgRef = React.useRef(null);

     const { _xl, _lg } = MediaQuery() ;

     useEffect(() => {
          Plot({ data, svgRef,_xl, _lg })
     },[data, svgRef,_xl, _lg])

     return(
          <svg ref={svgRef} width={'100%'} height={_xl?'142':'100'} />
     );
}
