


export const extractData = ( array, args, labels) => {
     
     const data = {} ;

     labels.forEach( label => data[label]=[] ) ;
  
     if( Array.isArray(array) ){
          array.forEach( (obj, idxObj ) => {
               if( obj?.resource?.total > 0 ){
                    
                    obj.resource.entry[0].resource.code.coding
                         .forEach( (coding,idxLabel) => data[`${Object.keys(data)[idxObj]}`].push({
                              label: args[args.length === labels.length ? idxObj : idxLabel],
                              value: coding.display
                         }));
               };
          });
     };

     return( data ) ;
};