
import React, {useEffect,  useRef} from "react";
import * as d3 from "d3";
import { MediaQuery } from '../../MediaQuery';

const Plot = (data, prom, _xl, _lg, svgSindro) => {

     const yData = data.map( item => item[0] ).reverse() ;
     const xData = data.map( item => item[1] );

     const fontSize = _xl ? '22px' : _lg ? '16px': '12px' ;

     const margin = {
               right:25,
               top:10,
               bottom: _xl ? 65 : _lg ? 55: 43,
               left: _xl ? 45 : _lg ? 40: 38,
          } ;

     const svgEl = d3.select(svgSindro.current);
     svgEl.selectAll("*").remove(); // Clear svg content before adding new elements

     const width = svgEl._groups[0][0].clientWidth  ;
     const height = svgEl._groups[0][0].clientHeight  ;

     const svg = svgEl.append("g")
                    .attr("width", `${width }`)
                    .attr("height",`${height}`)

     // Parametros del eje horizontal
     const xScale = d3.scaleTime()
                    .domain( d3.extent( xData, d => new Date(d) )  )
                    .range([margin.left, width-margin.right] ) ;

     const xScaleData = d3.scaleLinear()
                    .domain( [ 0 , data.length ] )
                    .range([margin.left, width-margin.right] );

     const x_axis = d3.axisBottom()
                    .scale(xScale) ;

     // Insercion del eje x
     svg.append("g")
          .attr("transform", `translate(0, ${height-margin.bottom})`)
          .call(x_axis)
          .attr("color", 'black' )
          .attr('opacity','0.85')
          .selectAll("text")
          .attr("transform","rotate(45,-8,15)")
          .attr('style', `font-size:${fontSize}; font-family: Montserrat !important`)


     // Parametros de eje vertical
     const limits = d3.extent( yData, d => parseFloat(d) ).reverse() ;

     const yScale = d3.scaleLinear()
                    .domain( [limits[0], limits[1] ] )
                    .range([margin.top , height - margin.bottom ]) ;

     const yAxis = d3.axisLeft()
                    .scale(yScale)
                    .tickSize(-width + margin.right + margin.left )
                    .ticks(5) ;

     // Insercion del eje y
     svg.append("g")
          .attr("transform", `translate(${margin.left} ,0)`)
          .call(yAxis)
          .attr("color", 'black' )
          .attr('opacity','0.85')
          .attr('style', `font-size:${fontSize}; font-family: Montserrat !important`)
          .call(g => g.select(".domain") // Quita la primera linea continua y la linea vertical continua
               .remove()
          )
          .call(g => g.selectAll("line") // .tick:not(:first-of-type)
               .attr("stroke-opacity", "0.3")
          ) ;

     // Grafica : datos
     svg.append("path")
          .datum(yData)
          .attr("fill", 'none')
          .attr("stroke", '#80A9FF')
          .attr("stroke-width", '2')
          .attr("d", d3.line()
               .x( (d,i) => xScaleData(i) )
               .y( (d,i) => yScale( d ) )//!parseFloat(d) ? parseFloat(prom) : parseFloat(d)) )
               .curve( d3.curveCatmullRom )
          );

     // Grafica : promedio
     svg.append("path")
          .datum(yData)
          .attr("fill", 'none')
          .attr("stroke", '#d51919')
          .attr("stroke-width", '1.4')
          .attr("d", d3.line()
               .x( (d,i) => xScaleData(i) )
               .y( (d,i) => yScale( parseFloat(prom) ) )
               .curve( d3.curveCatmullRom )
          );

};

export const PlotGrafica = ( {data, prom} ) => {

     const {_xl, _lg} = MediaQuery() ;

     const svgSindro = useRef(null);

     useEffect( ()=> {
          Plot(data, prom, _xl, _lg, svgSindro);
     },[data, prom, _xl, _lg, svgSindro ]);


     return(
          <svg ref={svgSindro} width={'100%'} height={_xl ? '300px' : _lg ? '220px':'200px'} />
     );
};
