import React, {useState, useEffect} from 'react';
import { Stl } from '../../Stl';
import { Box } from '@mui/material';
import { Grid } from '@material-ui/core';
import { MediaQuery } from '../../MediaQuery';
import Button from '@mui/material/Button';
import Stack from '@mui/material/Stack';

// import { SubTitle } from '../elements/SubTitle' ;
import { Title } from '../elements/Title' ;
import { getDataEnfermeria  } from './helpers/getDataEnfermeria';

import { PlotGantt } from './PlotGantt' ;

export const Enfermeria = ({dt}) => {

     const classes = Stl() ;
     const {_xl, _lg} = MediaQuery() ;
     const [ id, dayNow ] = dt ; // dayNow: [ 23, 3, 2023 ]

     // const fieldsIzq = [
     //      'Aseo y Confort','Aseo Matinal','Bao en Cama','Lavado de Pelo','Aseo Genital','Aseo Cavidades','Aseo Ocular',
     //      'Prevencin UPP','Colchn ant-esc.','Movilizacin','Riesgo Medio(4h)','Riesgo alto(2h)','Medidas Aisl.','Bao Clorex',
     //      'Aseo Bucal'
     // ];

     // const fieldsDer = [
     //      'Procedimientos','Curacin CVC','Cms0','Sitio de insercin','Puntos de fijacin','UPP','Ubicacin','Grado',
     //      'Instalacin Dispositivos','VVP/LA/SF','Cms1','Cms2','Cms3'
     // ];

     const [data, setData] = useState({});
     const [jornada, setJornada] =  useState({state:'Da'});

     useEffect(()=>{
          const dt = async() => {
               await getDataEnfermeria( id, dayNow, setData ) ;
          }
          dt() ;
     },[id, dayNow]);

     console.log( data, jornada.state);

     const BtnJornada = ({set}) => {

          return(
               <Stack 
                    style={{margin:'0.5rem', position:'relative'}}
                    spacing={1} 
                    direction="row"
               >
                    {
                         ['Da', 'Noche'].map( (jrd, idx) => 
                              <Button 
                                   key={idx}
                                   variant={ jrd === jornada.state ? 'outlined' : 'contained'  }
                                   onClick={ event => { set({ state:event.target.firstChild.data }) } }
                                   style={{fontSize: _lg ? '0.9rem': '0.7rem'}}
                              > 
                                   {jrd} 
                              </Button>  
                         )
                    }
               </Stack>
          )
     };

     const styleLabels = {
          fontFamily:'Montserrat',
          fontWeight:'bold',
          color:'rgba(0, 91, 165, 1)',
          paddingBlock:'0.2rem',
          marginLeft:'0.5rem',
          fontSize: _lg ? '1.3rem': '0.8rem',
          lineHeight: _lg? '1.4rem':'0.85rem'
     };

     const styleSinAsignar ={
          fontFamily:'Montserrat',
          background:'rgba(0, 91, 165, 1)',
          color:'white',
          margin:'0.4rem',
          padding:'0.3rem',
          width:'fit-content',
          borderRadius:'0.3rem',
          fontSize: _lg ? '1.1rem': '0.7rem'
     };

     const styleSi = {
          ...styleLabels, 
          border:'1px solid rgba(0, 91, 165, 1)', 
          borderRadius:'0.3rem', 
          padding:'0.3rem',
          margin: '0.4rem'
     };

     const styleNo = {
          ...styleSinAsignar, 
          border:'1px solid rgba(0, 91, 165, 1)', 
          borderRadius:'0.3rem', 
          padding:'0.3rem'
     };

     const styleName = {
          fontFamily:'Montserrat',
          marginLeft:'0.5rem',
          fontWeight:'500',
          fontSize:  _lg ? '1.3rem': '0.8rem'
     };

     return(
          <Box 
               mt={2} pb={1}
               style={{background:'white',height: _xl?'930px': _lg?'805px':'685px'}}
          >
               <div 
                    className={[classes.text_white, classes.bg_blue, classes.my_0].join(' ')}
               >
                    <Title >
                         Panel de Eventos
                    </Title>
               </div>

                  
               <Grid
                    container
                    direction="row"
                    justifyContent="space-evenly"
                    alignItems="flex-start"
               >

                    <Grid item xs={3}>
                         <BtnJornada set={setJornada} />
                         <div id={'labels'}>
                              {    
                                   data[jornada.state === 'Da' ? 'dia' : 'noche'] //(data.dia || data.noche )
                                   ?
                                        data[jornada.state === 'Da' ? 'dia' : 'noche'].filter( obj => obj.time )
                                             .filter( obj => obj.label.search('NOMBRE') < 0)
                                             .map( (obj,idx) => 
                                                  <div key={idx} style={styleLabels} >
                                                       {obj.label}
                                                  </div>
                                             )
                                   :    <span></span>
                              }
                         </div>
                    </Grid>
                    <Grid item xs={9}>
                         <PlotGantt data={data} jornada={jornada.state}/>
                    </Grid>
                    {
                         // <hr width={800} />
                    }
               </Grid>

               <Box 
                    mt={3} 
               >
                    <Grid
                         container
                         direction="row"
                         justifyContent="space-evenly"
                         alignItems="flex-start"
                         spacing={2}
                    >
                         <Grid item xs={6}>
                              <Title color='blue' >
                                   Sin asignar
                              </Title>
                              <div style={{display:'flex', flexGrow: '4',flexWrap: 'wrap'}}>
                                   {    
                                        data.sinAsignar
                                        ?
                                             data.sinAsignar.map( (obj,idx) => 
                                                  <div key={idx} style={styleSinAsignar}>
                                                       {obj.label}
                                                  </div>
                                             )
                                        :    <span></span>
                                   }
                              </div>
                         </Grid>
                         
                         <Grid item xs={6}>
                              <Title color='blue' >
                                   Contencin fsica { jornada.state === 'Da' ? 'diurna' : 'nocturna'}
                              </Title>
                              {
                                   data[jornada.state === 'Da' ? 'dia' : 'noche'] //(data.dia || data.noche )
                                   ?
                                        <div style={{display:'flex', flexGrow: '4',flexWrap: 'wrap'}}>
                                             {
                                                  data[jornada.state === 'Da' ? 'dia' : 'noche']
                                                       .filter( obj => !obj.time & obj.label.search('NOMBRE') )
                                                       .map( (obj,idx) => 
                                                            <div key={idx} style={ obj.value === 'Si' ? styleSi : styleNo }>
                                                                 {obj.label} : {obj.value}
                                                            </div>
                                                       )
                                             }
                                        </div>
                                        
                                   :    <span></span>
                              }

                              <div style={{marginTop:'2rem'}}>
                                   <Title color='blue' >
                                        Enfermero/a turno { jornada.state === 'Da' ? 'da' : 'noche'}
                                   </Title>
                                   <div style={{marginTop:'0.5rem'}}>
                                        {    
                                             data[jornada.state === 'Da' ? 'dia' : 'noche']
                                             ?
                                                  data[jornada.state === 'Da' ? 'dia' : 'noche']
                                                       .filter(obj => obj.label.search('NOMBRE') > -1 )
                                                       .map( obj =>
                                                            <div style={styleName}>
                                                                 {obj.value}
                                                            </div>
                                                       )
                                             :    <span></span>
                                        }
                                   </div>
                              </div>
                         </Grid>
                    </Grid>

               </Box>


          </Box>
     );

};
