import React,{useEffect} from "react";
import * as d3 from "d3";
import { MediaQuery } from '../../MediaQuery';

export const PlotDailyCum =({data})=>{



     const input = data.input.map( (obj,idx) => {
          return(
               {
                    name: obj.text.split(':')[1],
                    value: parseFloat( obj.data[2].display )
               }
          )
     });
     input.pop();

     const output = data.output.map( (obj,idx) =>{
          return(
               {
                    name: obj.text.split(':')[1],
                    value: parseFloat( obj.data[2].display )
               }
          )
     });
     output.pop();

     // suma vectorial
     // const sumVec = (v1,v2) =>{
     //      let vs = [];
     //      for( let idx = 0; idx < d3.max([v1.length,v2.length]) ; idx++){
     //           vs.push( (v1[idx]?v1[idx]:0) + (v2[idx]?v2[idx]:0) );
     //      }
     //      return vs
     // };

     const color_fill = 'rgba(0, 91, 165, 1)';
     const color_bg = '#DCF0F4' ;
     const columnPos = '#005BA5' ;
     const columnNeg = '#003159' ;
     const {_xl, _lg} = MediaQuery() ;

     const svgRef = React.useRef(null);
     const margin = {top:15, bottom: 15, left: _xl?55:40, right:_xl?65:60, w:_xl?60:32 ,offSetY:100} ;

     useEffect( () => {

          const svgEl = d3.select(svgRef.current);
          svgEl.selectAll("*").remove(); // Clear svg content before adding new elements

          const width = svgEl._groups[0][0].clientWidth  ;
          const height = svgEl._groups[0][0].clientHeight  ;


          const svg = svgEl.append("g")
                         .attr("width", `${width}`)
                         .attr("height",`${height}`)

          // fondo
          svg.append('rect')
               .attr('x',0)
               .attr('y',0)
               .attr('width','100%')
               .attr('height','100%')
               .style("fill", color_bg )

          // Construccion del eje x:
          //limites del ejes
          let nRects = d3.max([input.length,  output.length]);

          const xScale = d3.scaleLinear()
                              .domain([1 , nRects])
                              .range([margin.left+5+margin.w, width-margin.right]) ;

          const yScaleAxis = d3.scaleLinear()
                                   .domain( [ d3.max( input.map(d => d.value) ), -d3.max( output.map(d => d.value) ) ] )
                                   .range( [ margin.top, height-margin.bottom ] );

          // eje y
          svg.append("g")
               .attr("transform", `translate( ${margin.left} ,0)`)
               .call(
                    d3.axisLeft(yScaleAxis)
                    .tickSizeOuter(0)
                    .ticks(7)
                    .tickSizeInner( -width + margin.right )
               )
               .call( g => g.selectAll(".tick line")
                    .attr("stroke-opacity", 0.4)
                    .attr("stroke-dasharray", "2,3"))
               .attr("font-family", "Montserrat")
               .attr("font-size", _xl?'13px':'10px')
               .attr("color", color_fill )
               .attr("opacity",'0.75')

          // eje x
          svg.append("g")
               .attr("transform", `translate( 0 , ${yScaleAxis(0)})`)
               .call(d3.axisBottom(xScale))
               .attr("font-family", "Montserrat")
               .attr("font-size", '0px')
               .attr("color", color_fill )

          //Columnas Positivas
          svg.append('g')
               .selectAll('rect')
               .data( input )
               .enter()
               .append('rect')
               .attr('x' ,(d,i) => xScale(i+1) )
               .attr('y', d => yScaleAxis(d.value) )
               .attr('height', (d) => yScaleAxis(0)-yScaleAxis(d.value) )
               .attr('width', margin.w )
               .attr('fill', columnPos )
               .attr("opacity", 0.80)

          // nombres y sombra de columna
          svg.append('g')
               .selectAll('rect')
               .data( input )
               .enter()
               .append('rect')
               .attr('x' ,(d,i) => xScale(i+1) )
               .attr('y', margin.top )
               .attr('height', yScaleAxis(0) - margin.top )
               .attr('width', margin.w )
               .attr('fill', columnPos )
               .attr("opacity", 0.2);
          svg.selectAll(null)
               .data( input )
               .enter()
               .append("text")
               .text( d => !_lg ? d.name.slice(0,7).replace(/[^a-zA-Z]/g,'') + '.' : d.name )
               .attr("x", (d,i) => xScale(i+1) - (!_lg ? 7 : d.name.length)*1.2 )
               .attr("y", d => yScaleAxis(d.value) - 3 )
               .attr("font-family", "Montserrat")
               .attr("font-size", '12px' )
               .attr("fill", columnPos );

          // Columnas Negativas
          svg.append('g')
               .selectAll('rect')
               .data( output )
               .enter()
               .append('rect')
               .attr('x' ,(d,i)=> xScale(i+1)-margin.w )
               .attr('y', yScaleAxis(0) )
               .attr('height',(d) => yScaleAxis(0)-yScaleAxis(d.value) )
               .attr('width', margin.w )
               .attr('fill', columnNeg )
               .attr("opacity", 0.80);

          // nombres y sombra de columna
          svg.append('g')
               .selectAll('rect')
               .data( output )
               .enter()
               .append('rect')
               .attr('x' ,(d,i) => xScale(i+1)-margin.w )
               .attr('y', yScaleAxis(0) )
               .attr('height', height/2 - 28 )
               .attr('width', margin.w )
               .attr('fill', columnNeg )
               .attr("opacity", 0.2)
          svg.selectAll(null)
               .data( output )
               .enter()
               .append("text")
               .text( d => !_lg ? d.name.slice(0,7).replace(/[^a-zA-Z]/g,'') + '.' : d.name  )
               .attr("x", (d,i) => xScale(i+1)-margin.w - (!_lg ? 7: d.name.length)*0.6 )
               .attr("y", d => yScaleAxis(-d.value) + 8 )
               .attr("font-family", "Montserrat")
               .attr("font-size", '12px' )
               .attr("fill", columnPos );
     });

     return(
          <svg ref={svgRef} width={'100%'} height={_xl?'235':_lg ?'200' :'208'} />
     );
}
