import React,{useEffect} from "react";
import * as d3 from "d3";
import { MediaQuery } from '../../MediaQuery';


export const PlotSindromatico =({data,n})=>{

     const {_xl,_lg} = MediaQuery();

     const svgSindro = React.useRef(null);

     useEffect(() => {
          Plot( svgSindro, data, n, _xl, _lg );
     },[data, n, _xl, _lg ])

     return(
          <svg ref={svgSindro} width={'100%'} height={ _xl ? '322' : _lg? '258' :'221' } />
     );
};


const Plot = ( svgSindro, data, nVertices, _xl, _lg ) => {

     const svgEl = d3.select(svgSindro.current);
     svgEl.selectAll("*").remove(); // Clear svg content before adding new elements

     const margin = {top:10, right: 2, bottom: 15, left: 2, m:2} ;
     const color_fill = 'rgba(0, 91, 165, 1)' ;
     const ofst_mag = _xl ? 20 : _lg ? 18 : 15 ;


     const width = svgEl._groups[0][0].clientWidth - margin.left - margin.right ;
     const height = svgEl._groups[0][0].clientHeight - margin.top - margin.bottom ;

     const svg = svgEl.append("g")
                    .attr("width", `${width }`)
                    .attr("height",`${height}`) ;

     const pi = Math.PI, ang_i = 2*pi/nVertices;

     const Cos = (ang) => Math.cos(ang) ;
     const Sen = (ang) => Math.sin(ang) ;

     const beta = 20*pi/180 ; // Define el angulo de los puntos adyacentes a la linea pricipal del rombo
     const a =  0.65 ;   // Fraccin del radio

     const rb = _xl ? 45 : _lg ? 30 : 15 ;
     const r =  (width - rb*margin.left - rb*margin.right)/2;

     const fontSize = _xl ? '22px' : _lg ? '16px': '12px' ;
     const fontLabel = _xl ? '25px' : _lg ? '19px': '15px' ;

     //Centro del plano
     const xc = width/2;
     const yc = height/2 ;

     // puntos en el plano, vertices del poligono
     const pX = (i) => r*Cos(i*ang_i) + xc  ;
     const pY = (i) => r*Sen(i*ang_i) + yc  ;

     // Creacion de datos para los  vertices
     const points_poly = [...Array(nVertices)].map( (_,idx) => {

          //Creacion de los rombos:

          // Escala del parametro variable:
          let varScale = d3.scaleLinear()
               .domain( data[idx].dom )
               .range([r/3,r]);

          // Escala de color
          let colorScale = d3.scaleLinear()
               .domain( data[idx].dom )
               .range( data[idx].color );

          // rombo, p longitud del rombo
          let p = data[idx].value < data[idx].dom[0]
                    ? varScale( data[idx].dom[0] )
                    : data[idx].value > data[idx].dom[1]
                         ? varScale( data[idx].dom[1] )
                         : varScale(data[idx].value) ;

          let rombo = [] ;
          rombo.push([xc,yc]) ; // vertice centro
          rombo.push( [xc + a*p*Cos(beta+ang_i*idx) , yc + a*p*Sen(beta+ang_i*idx)] ) ; // vertice paralelo 1
          rombo.push( [xc + p*Cos(ang_i*idx) , yc + p*Sen(ang_i*idx)] ) ; // vertice  extremo
          rombo.push( [xc + a*p*Cos(-beta+ang_i*idx) , yc + a*p*Sen(-beta+ang_i*idx)] ) ; // vertice paralelo 2

          let color = colorScale(
                              data[idx].value < data[idx].dom[0]
                                   ? data[idx].dom[0]
                                   : data[idx].value > data[idx].dom[1]
                                        ? data[idx].dom[1]
                                        : data[idx].value
                         )

          // dibujo del rombo
          svg.append("polygon")
               .attr("fill", color )
               .attr("stroke", color )
               .attr("stroke-width", 3)
               .attr("points", d3.polygonHull(rombo) )
               // .attr("opacity",'0.75')

          // texto de magnitud
          svg.append("text")
               .text((d) => data[idx].value )
               .attr("x", rombo[2][0] )
               .attr("y", (d,i) => { 
                    if( rombo[2][1]  > yc){
                         return rombo[2][1] + ofst_mag
                    };
                    return rombo[2][1]
               })
               .attr("font-family", "Montserrat")
               .attr("font-size", fontSize )
               .attr("fill", color_fill )
               .attr("text-anchor", (d,i) => {
                    if( rombo[2][0] < xc ){
                         return "start"
                    }
                    return 'start'
               })

          return [pX(idx), pY(idx)]
     });

     // poligono padre
     svg.append("polygon")
          .attr("fill", "none")
          .attr("stroke", color_fill)
          .attr("stroke-width", _xl?2:1)
          .attr("points", d3.polygonHull(points_poly) )
          .attr("stroke", color_fill);

     const textTotal = (e, d, flag) => {
          
          if(flag){
               document.getElementById(`${d.name}_id`).innerHTML =  d.name ;
          }
          else{
               document.getElementById(`${d.name}_id`).innerHTML =  '';
          }
     };

     // nombres
     svg.selectAll(null)
          .data( data )
          .enter()
          .append("text")
          .text( d => d.name.slice(0,3) )
          .attr("x", (d,i) => pX(i) )
          .attr("y", (d,i) => pY(i) )
          .attr("font-family", "Montserrat")
          .attr("text-anchor", (d,i) => {
               if( pX(i) <= xc ){
                    return "end"
               }
               return 'start'
               
          })
          .attr("font-size", fontSize)
          .attr("fill", color_fill ) ;

     // label 
     svg.selectAll(null)
          .data( data )
          .enter()
          .append("text")
          .attr('id', (d,i) => `${d.name}_id`)
          .attr('x', width/2)
          .attr('y', height)
          .attr("font-family", "Montserrat" )
          .attr("font-size", fontLabel )
          .attr("fill", color_fill ) 
          .attr("text-anchor", 'middle')

     // rectangulos de eventos de mouse
     const svgRect = svgEl.append("g")
                    .attr("width", `${width }`)
                    .attr("height",`${height}`) ;

     svgRect.selectAll('rect')
          .data( data )
          .enter()
          .append('rect')
          .attr("height",25 )
          .attr("width", 40 )
          .attr("fill","red")
          .attr('opacity', 0)
          .attr('x', (d,i) =>  pX(i)-20 )
          .attr('y', (d,i) =>  pY(i)-15 )
          .on( 'mouseover', (e,d) => textTotal(e,d,true))
          .on( 'mouseout', (e,d) => textTotal(e,d,false)) ;


     //Radios
     svg.selectAll("line")
          .data( points_poly )
          .enter()
          .append("line")
          .attr("fill", color_fill)
          .attr("stroke", color_fill)
          .attr("stroke-width", _xl?1.5:0.5 )
          .attr("x1", xc )
          .attr("y1", yc )
          .attr("x2", d => d[0] )
          .attr("y2", d => d[1] );
};
