import React,{useState} from 'react';
import { Box,  Grid, Button} from '@mui/material';
// import { MediaQuery } from '../../MediaQuery';
import {Stl } from '../../Stl'

import { SubTitle } from '../elements/SubTitle' ;
import { Field } from '../elements/Field' ;

export const Tabs = (stl)=>{

     const classes = Stl() ;
     // const {_lg} = MediaQuery() ;

     const [idx,setIdx] = useState({
          value:0
     });
     const changeIdx = (idx)=>{
          setIdx({value:idx})
     };

     return(
          {
               tabObj:()=>{
                    return(
                         <Box
                              mt={1} px={1} py={1}
                              className={classes.bg_white}
                         >
                              <Grid
                                   container
                                   direction='row'
                                   wrap= { 'nowrap' }
                                   spacing={1}
                                   coloumns={21}
                              >
                              {
                                   [    'DASHBOARD',
                                        'LABORATORIO',
                                        'IMAGENES',
                                        'RESPIRATORIO',
                                        'ENFERMERA',
                                        'BALANCE',
                                        'HISTORIA'
                                   ].map( (value,indx) =>

                                        <Grid
                                             item xs={3}
                                             key={indx}
                                             style={{display:'flex'}}
                                        >
                                             <Button
                                                  variant='contained'
                                                  color='primary'
                                                  fullWidth={true}
                                                  className={ [indx===idx.value ? classes.bg_white : classes.bg_blue , classes.button_tab].join(' ') }
                                                  onClick={ ()=>{ changeIdx(indx) } }
                                             >
                                                  {
                                                       indx===idx.value
                                                            ? <Field color={'blue'}>
                                                                 {value}
                                                              </Field>
                                                            : <SubTitle color={'white'}>
                                                                 {value}
                                                              </SubTitle>
                                                  }

                                             </Button>
                                        </Grid>
                                   )}
                              </Grid>
                         </Box>
                    )
               },
               tabIndex:idx.value
          }
     );
};
