
import React, {useState} from 'react' ;
import { Grid } from '@mui/material';
import { SubTitle } from '../elements/SubTitle' ;

import { styled } from '@mui/system';
import { MediaQuery } from '../../MediaQuery' ;

import { Tabla }  from '../helpers/Tabla';

import '../../stylesOverrides.css'


export const Block = ({data}) => {

     const {_xl, _lg} = MediaQuery() ;

     const [displayCuadro, setDisplay] = useState({
          state:'hidden'
     });

     const handleClick = () => {
          if( displayCuadro.state === 'hidden'){
               setDisplay({state:'visible'})
          }else{
               setDisplay({state:'hidden'})
          };
     };


     const StyleTitle = styled('div')({
          fontSize: _xl ? '25px' : _lg ? '20px': '14px',
          fontFamily:'Montserrat',
          border:'2px solid rgb(00,80,255,80%)',
          background: 'rgb(00,80,255,30%)',
          borderRadius:'3px',
          cursor:'pointer',
          color:'rgb(70,70,70)',
          textAlign:'center',
          paddingInline: '0.5rem',
          marginRigth : '0.5rem',
          width:'100%',
          paddingBlock:'0.2rem',
          '&:hover':{
               border:'2px solid #0d50a1',
               background: 'rgb(00,80,255,10%)'
          }
     });

     const Cuadro = styled('div')({
          boxShadow: 'rgba(0, 0, 0, 0.4) 5px 5px 5px 3px',//boxShadow: 'rgb(80,80,255,40%) 4px 4px',
          visibility:displayCuadro.state,
          border:'1px solid rgb(100,100,100,60%)',
          borderRadius:'3px',
          fontFamily:'Montserrat',
          paddingLeft:  '0.8rem',
          paddingTop:'0.3rem',
          color:'rgb(70,70,70)',
          position: 'absolute',
          // top: _xl ? '15%' :_lg ? '35%' : '12%',
          // left: _xl ? '27.5%' :_lg ? s'28.5%' : '29%',
          width: _xl ? '1000px' : _lg ? '400px':'300px',
          background:'rgb(255,255,255,95%)',
          '&:hover':{
               zIndex:'1000',
          }
     });

     const Svg = styled('svg')({
          borderRadius:'3px',
          marginRight:'0.2rem',
          cursor:'pointer',
          '&:hover':{
               background: 'rgb(00,80,255,30%)'
          }
     });

     return(
          <Grid
               item  container
               align='center'
               xs={4}
          >
               <StyleTitle onClick={ ()=> handleClick() }>
                    <strong> {data.title1} </strong>
               </StyleTitle>


               <Cuadro>
                    <div
                         style={{ display: 'flex', justifyContent: 'space-between',}}
                    >
                         <SubTitle>
                              {data.title2}
                         </SubTitle>
                         <div>
                              <span
                                   onClick={ ()=> handleClick()}
                              >
                                   <Svg
                                        width={_xl ? '45' : '30'} height={_xl ? '45' : '30'}
                                        viewBox="0 0 30 30" fill="none" xmlns="http://www.w3.org/2000/svg"
                                   >
                                        <path
                                             d='M26.3 0.8H9.3C7.7 0.8 6.4 2.1 6.4 3.6V20.6C6.4 22.2 7.7 23.4 9.3 23.4H26.3C27.9 23.4 29.1 22.2 29.1 20.6V3.6C29.1 2.1 27.9 0.8 26.3 0.8ZM26.3 20.6H9.3V3.6H26.3V20.6ZM3.6 6.4V26.3H23.4V29.1H3.6C2.1 29.1 0.8 27.9 0.8 26.3V6.4H3.6ZM11.8 16.1L15.8 12.1L11.8 8.1L13.8 6.1L17.8 10.1L21.8 6.1L23.8 8.1L19.8 12.1L23.8 16.1L21.8 18.1L17.8 14.1L13.8 18.1L11.8 16.1Z'
                                             fill="black"
                                             fillOpacity="0.65"
                                        />
                                   </Svg>
                              </span>
                         </div>
                    </div>

                    <Tabla data={data.body} />

               </Cuadro>
          </Grid>
     );
};


