import React,{useState,useEffect} from 'react' ;
import { Grid } from '@mui/material' ;
//Componentes
import { DashBox } from './DashBox ';
import { DashContentGraficas} from './DashContentGraficas' ;
import { BoxSindromatico } from './BoxSindromatico' ;
import { PlotMedicacion } from './PlotMedicacion' ;
import { Ventilacion } from './Ventilacion' ;
// Estilos
import { Stl } from '../../Stl' ;
// helpers
import { getMedicacion } from './helpers/getMedicacion';
import { dataFormat, variables } from './helpers/dataFormat' ;
import { getInfo }  from './helpers/getInfo' ;

export const Dashboard = ( {dt} ) => {

     const classes = Stl() ;

     const [ id , dayNow  ] = dt ;

     const [vitalSings, setVitalSings] = useState({
          ...dataFormat(variables)
     });

     const cuadros = [
          {key:"Sepsis-Inflamatorio",obj:vitalSings.sepsis},
          {key:"Shock-Hemodinmico",obj:vitalSings.shock},
          {key:"Ventilatorio",obj:vitalSings.ventilatorio},
          {key:"Transplante",obj:vitalSings.transplante},
          {key:"Renal",obj:vitalSings.renal},
          {key:"Neurologco",obj:vitalSings.neurologico}
     ];

     const [medicacion, setMedicacion] = useState([
          {
               categoria:'Categora',
               medicamento:'Medicamento',
               intervaloValue:'h',
               intervaloUnit:'#',
               date: new Date(),
               dosisValue:'1',
               dosisUnit:'ml,g'
          }
     ]);

     
     useEffect( () => {
          getMedicacion( id, dayNow, setMedicacion);
          getInfo( id, dayNow, setVitalSings );
     }, [id,dayNow] );

     return(
          <>
               <Grid
                    container
                    spacing={1}
               >
                    <Grid
                         item
                         xs={9}
                    >
                         <Grid
                              container
                              spacing={1}
                         >
                              {
                                   [
                                        ['Presin Arterial', vitalSings.PresArterial],
                                        ['Frecuencia Cardiaca', vitalSings.FrecCardiaca],
                                        ['Temperatura Corporal', vitalSings.TempCorporal ],
                                        ['Frecuencia Respiratoria', vitalSings.FrecRespiratoria ]
                                   ].map( (plot,idx) =>
                                        <Grid
                                             item
                                             xs={6}
                                             key={idx}
                                        >
                                             <DashBox
                                                  stl={classes}
                                                  title={plot[0]}
                                             >
                                                  <DashContentGraficas
                                                       stl={classes}
                                                       data={plot[1]}
                                                  />
                                             </DashBox>
                                        </Grid>
                                   )
                              }

                              <Grid
                                   item
                                   xs={12}
                              >
                                   <DashBox
                                        stl={classes}
                                        title='Medicacin'
                                   >
                                        <PlotMedicacion
                                             data={ medicacion }
                                             date={ dayNow }
                                        ></PlotMedicacion>
                                   </DashBox>
                              </Grid>
                              <Grid
                                   item
                                   xs={12}
                              >
                                   <DashBox
                                        stl={classes}
                                        title='Ventilacin'
                                   >
                                        <Ventilacion
                                             stl={classes}
                                             dt={[id , dayNow]}
                                        ></Ventilacion>
                                   </DashBox>
                              </Grid>

                         </Grid>

                    </Grid>

                    <Grid
                         item
                         xs={3}
                    >
                         <BoxSindromatico
                              stl={classes}
                              title='Cuadro Sindromtico'
                              data={cuadros}
                         >
                         </BoxSindromatico>

                    </Grid>



               </Grid>
          </>
     );

};
