
import React,{useState} from 'react' ;
import { styled } from '@mui/system';
import { MediaQuery } from '../../MediaQuery' ;

export const Info = () =>{

     const {_xl, _lg} = MediaQuery() ;

     const [displayCuadro, setDisplay] = useState({
          state:'none'
     });

     const Cuadro = styled('div')({
          boxShadow: 'rgb(80,80,255,40%) 4px 4px',
          display:displayCuadro.state,
          border:'1px solid rgb(100,100,100,30%)',
          borderRadius:'3px',
          fontFamily:'Montserrat',
          paddingLeft:  '0.8rem',
          paddingTop:'0.3rem',
          color:'rgb(70,70,70)',
          position: 'absolute',
          bottom: '2rem',
          left: _lg ? 'calc(74.4%)' : 'calc(74%)',
          width: _xl ? '595px' : _lg? '433px':'292px',
          background:'rgb(255,255,255,87%)'
     })

     const StyleInfo = styled('div')({
          fontSize: _xl ? '25px' : _lg ? '20px': '14px',
          fontFamily:'Montserrat',
          border:'2px solid rgb(00,80,255,80%)',
          background: 'rgb(00,80,255,30%)',
          borderRadius:'3px',
          cursor:'pointer',
          color:'rgb(70,70,70)',
          textAlign:'center',
          paddingBlock:'0.2rem',
          '&:hover':{
               border:'2px solid #0d50a1',
               background: 'rgb(00,80,255,10%)'
          }
     });


     const handleClick = (value) => {
          if( displayCuadro.state === 'none'){
               setDisplay({state:'block'})
          }else{
               setDisplay({state:'none'})
          };
     };
     return(
          <>
               <StyleInfo onClick={ ()=> handleClick() }>
                    <strong> Informacin </strong>
               </StyleInfo>

               <Cuadro onClick={ ()=> handleClick() }>
                    {
                         [
                              ['Shift + Mouse Wheel:','En las grficas de flujo gobierna el intervalo de tiempo a visualizar.'],
                              ['Shift + Mouse Click','Permite la exploracin lenta de las grficas de flujo.'],
                              ['Mouse Click','Permite dibujar rectangulos para definir el zoom sobre los grficos de flujo.'],
                              ['Drag & Drop',"Las grficas de los KPI's se pueden arrastrar para organizar una visulizacin conveniente."]
                         ].map( (item,idx)=>
                              <div
                                   key={idx}
                                   style={{fontSize: _xl ? '22px' : _lg ? '16px': '12px',}}
                              >
                                   <strong>
                                        {item[0]}
                                   </strong>
                                   <p
                                        style={{marginBlock:'0.3rem'}}
                                   >
                                        {item[1]}
                                   </p>
                              </div>
                         )
                    }
               </Cuadro>
          </>
     )
};
