import React, {useEffect } from "react";
import * as d3 from "d3";

import { MediaQuery } from '../../MediaQuery';

const margin = { right:57, top:10,  bottom: 10, left: 40 } ;


// funcion que gobierna el svg:
const plot = (interval, svgSindro, set, point) => {

     const svgEl = d3.select(svgSindro.current);
     svgEl.selectAll("*").remove(); // Clear svg content before adding new elements

     const width = svgEl._groups[0][0].clientWidth  ;
     const height = svgEl._groups[0][0].clientHeight  ;

     const svg = svgEl.append("g")
                    .attr("width", `${width }`)
                    .attr("height",`${height}`)

     // Parametros del objeto
     const xScale = d3.scaleLinear()
                    .domain( interval )
                    .range( [margin.left, width-margin.right] );

     // line
     svg.append('line')
          .attr('id','line')
          .attr('x1', xScale(interval[0]) )
          .attr('x2', xScale(interval[1])+15 )
          .attr('y1', height/2 )
          .attr('y2', height/2 )
          .attr('stroke', '#525252')
          .attr('stroke-width','0.4rem')
          .attr('stroke-linecap','round')
          .attr('opacity','0.3');

     // objeto del BarSlider
     const bar = svg.append('g')
          .attr('id','moveMouse')
          .attr('fill','none')
          .attr('style','cursor:pointer')
          .attr('transform',`translate( ${ xScale(point) }, 11 ) scale(0.1)` )
          // .attr('opacity','0.5')
          .on('mouseover',()=>{
               svg.select(`#rect1`)
                    .attr('opacity','100')
               svg.select(`#line`)
                    .attr('opacity','100')
          })
          .on('mouseout',()=>{
               svg.select(`#rect1`)
                    .attr('opacity','0.3')
               svg.select(`#line`)
                    .attr('opacity','0.3')
          })

     bar.append('rect')
          .attr('width', '529')
          .attr('height', '369' )
          .attr('rx', '172')
          .attr('fill','#ffff')
          .attr('opacity','1');

     bar.append('rect')
          .attr('width', '529')
          .attr('height', '369' )
          .attr('rx', '172')
          .attr('fill','#0d47a1')
          .attr('id','rect1')
          .attr('opacity','0.3');

     // arrows
     ['M326 67L443.5 184.5L326 302',
      'M203.5 302L86 184.5L203.5 67'
     ].forEach( item => {
          bar.append('path')
               .attr('d', item )
               .attr('stroke', 'white')
               .attr('stroke-width', '37')
               .attr('stroke-linecap', 'round');
     });

     let  movX = 0, flag = true ;
     const dragHandler = d3.drag().on( 'drag', (event) => {

          if( flag ){
               movX = event.x ;
               flag = false ;
          }

          if( Math.abs(event.dx) < 200 ){
               movX = movX + ( event.sourceEvent.shiftKey ? event.dx/30 : event.dx );
          };

          if( (xScale.invert(movX) > 3) & ( xScale.invert(movX) < 98 )  ){
               set({value: xScale.invert(movX)-5 })
         };
     });

     dragHandler( svg.select(`#moveMouse`) );
};

export const BarraPlot = ( {set,point} ) => {

     const svgSindro = React.useRef(null);

     const {_xl, _lg} = MediaQuery() ;

     useEffect( ()=> {
          plot([0,100], svgSindro, set, point);
     },[svgSindro, set, point, _xl, _lg]);

     const styleSvg = {
          position: 'absolute',
          bottom: '3rem',
          left: 'calc(30%)'
     };

     return(
          <svg ref={svgSindro} width={_xl? '1100px':_lg ? '820px':'600px'} height={'60px'}  style={styleSvg}/>
     );

};
