import { unitToTwoUnit } from '../helpers/unitToTwoUnit';
import { getLastHour } from '../helpers/getLastHour';
import { resourceServer } from '../../env'

export const getLabels = (dayNow, id, setLabels) => {

     const dte = `${dayNow[2]}-${unitToTwoUnit(dayNow[1])}-${unitToTwoUnit(dayNow[0])}`;// 2022-08-10
     const hour = getLastHour('lastHour');
     const { urlServer, requestOptionsGet } = resourceServer;

     fetch(`${urlServer}Observation?identifier=${dte}${hour}&identifier=metricas&subject=${id}`, requestOptionsGet)
          .then(resp => {
               resp.text().then(obj => {

                    const data = JSON.parse(obj)

                    if (data.total > 0) {

                         const labels = data.entry[0].resource.component.filter(comp => comp.code.coding)
                              .map(cod => [
                                   cod.code.coding[0].display.split(':')[1], // Parametro
                                   cod.code.coding[1].display.split(':')[1], // Name
                                   cod.code.coding[2].display.split(':')[1], // unid
                                   cod.code.coding[3].display.split(':')[1], // varInterval
                                   cod.code.coding[4].display.split(':')[1]  // norInterval
                              ]);

                         setLabels(labels);
                    }

               })
          })

};
