import { unitToTwoUnit } from '../helpers/unitToTwoUnit';
import { processValueDate } from '../helpers/processValueDate';
import { resourceServer } from '../../env'

// pruebas directamente al API de VentSupport
// import { getDataVentsupport } from './getDataVentsupport';

export const getData =(dayNow, hour, id, setData, setMsn) => {

     const dte = `${dayNow[2]}-${unitToTwoUnit(dayNow[1])}-${unitToTwoUnit(dayNow[0])}` ;// 2022-08-10
     const { urlServer, requestOptionsGet } = resourceServer ;

     // getDataVentsupport();

     fetch(`${urlServer}Observation?identifier=${dte}${hour}&identifier=VentSupport_epochs_${id}&subject=${id}`,
          requestOptionsGet).then( resp => {
               resp.text().then( obj => {

                    const data = JSON.parse( obj )

                    if( data.total > 0){
                         const metricsObj = data.entry.map( entry => {
                              return(
                                   {
                                        name:entry.resource.identifier[1].value,
                                        body:entry.resource.component.map( comp => {
                                             return(
                                                  {
                                                       name:comp.code.text,
                                                       data: JSON.parse( comp.valueString )
                                                  }
                                             )
                                        })
                                   }
                              )
                         });

                         const metricStatistics = metricsObj.find( obj => obj.name === "metricas")
                                                       .body.map( item => {
                                                            return(
                                                                 {
                                                                      name : item.name,
                                                                      data: [...processValueDate(item.data)]
                                                                 }
                                                            );
                                                       });

                         setData([
                              ...metricStatistics,
                              ...metricsObj.filter( item => item.name !== "metricas")
                         ]);

                    }else{
                         setData([])
                         setMsn('Sin datos del respirador')
                    };

               })
          }).catch( err => {
               console.log("Error en getData de Ventilacion \n", err );
               getData(dayNow, id, setData, setMsn);
          });

};
