import { unitToTwoUnit } from '../helpers/unitToTwoUnit';
import { getLastHour } from '../helpers/getLastHour';
import { resourceServer } from '../../env'


export const getParametros = async(dayNow, id, label, setParametros) => {

     const dte = `${dayNow[2]}-${unitToTwoUnit(dayNow[1])}-${unitToTwoUnit(dayNow[0])}` ;// 2022-08-10
     const hour =  getLastHour( 'lastHour' ) ;
     const { urlServer, requestOptionsGet } = resourceServer ;


     await fetch(`${urlServer}Observation?identifier=${dte}${hour}&identifier=metricas&subject=${id}`,
          requestOptionsGet).then( resp =>
               resp.text().then( obj => {

                    const data = JSON.parse( obj )

                    if( data?.total > 0 ){

                         // const url = data.entry[0].fullUrl.split('v4/')[1] ;
                         const resource = data.entry[0].resource ;

                         let obj = resource.component.find( comp => comp.code.text === label ) ;

                         setParametros( ()=>obj.code.coding.map( item => item.display.split(':')[1] ) );

                    };

               })
          )

};
