
import React, {useState} from 'react';

import { Barra } from './Barra' ;

import { MediaQuery } from '../../MediaQuery' ;

const Input = ({value,idx})=>{

     const {_xl, _lg} = MediaQuery() ;

     const [label, setLabel] = useState([ value[0], value[1] ]) ;

     const onChange = (num,idx,event) => {
          // event.preventDefault();
          let valueArray = [
               [ num, label[1] ],
               [ label[0], num ]
          ][idx];

          return(
               setLabel(valueArray)
          );
     };

     return(
          <input
               type={'numb'}
               size={3}
               value={label[idx]}
               style={{caretColor: 'transparent',fontFamily:'Montserrat', fontSize:_xl ? '22px' : _lg ? '16px': '12px' }}
               onChange={ (event)=> onChange(
                    isNaN(parseInt(event.target.value))
                    ? 0
                    : parseInt(event.target.value),
                    idx, event )
               }

          />
     )
}

export const CellText = ({intervals, title}) => {

     const [normalInterval, setNormal] = useState(
          intervals[4].split('-')
               .map(n =>
                    parseFloat(n)
                         ? parseFloat(n).toFixed(2)
                         : 0
               )
     ) ;

     const {_xl, _lg} = MediaQuery() ;

     const [value, setValue] = useState(intervals[3].split('-').map( n=>parseInt(n)) ) ; // intervalo variable :

     const onBlur = (num,idx,event) => {

          if( num === value[1-idx] ) {
               num = num + 1
          }
          let valueArray = [ num, value[1-idx] ].sort( (a,b)=>a-b ) ;

          setValue( valueArray )
     };
     const onKeyDown = ( num,idx,event ) =>{

          if( event.key === "Enter" ){
               onBlur(num,idx)
          }
     };

     const intervalos = localStorage.getItem(`intervalos`)
                    ? JSON.parse( localStorage.getItem(`intervalos`) )
                    : {} ;

     intervalos[`${title}_intervalos`] = [value, normalInterval] ;
     localStorage.setItem(`intervalos`, JSON.stringify( intervalos, null, 2) )  ;

     return(
          <>
               {
                    ['min:','max:'].map( (field,idx) => {

                         return(
                              <div
                                   key={idx}
                                   align='center'
                                   style={{fontFamily:'Montserrat'}}
                                   onBlur={ (event)=> onBlur(
                                        isNaN(parseInt(event.target.value))
                                        ? value[idx]
                                        : parseInt(event.target.value),
                                        idx, event)
                                   }
                                   onKeyDown={ (event) =>onKeyDown(
                                        isNaN(parseInt(event.target.value))
                                        ? value[idx]
                                        : parseInt(event.target.value),
                                        idx,
                                        event )
                                   }
                              >
                                   <span
                                        style={{paddingInline:'0.8rem',  fontSize:_xl ? '22px' : _lg ? '16px': '12px'}}
                                   >
                                        {field}
                                   </span>

                                   <Input
                                        value={value}
                                        idx={idx}
                                   />

                              </div>
                         )
                    })
               }
               <p align='center'>
                    <Barra interval={value} setNormal={setNormal} normal={normalInterval}/>
               </p>

               <p
                    style={{fontFamily:'Montserrat',fontSize:_xl ? '22px' : _lg ? '16px': '12px' }}
               >
                    Rango sin alerta:
                    {
                         [
                              normalInterval[0],
                              normalInterval[1]
                         ].map( (label,idx) =>
                              <span key={idx} style={{fontWeight:'bold'}}>
                                    {' '+ label }  {idx === 0 ? ' - ': ' '}
                              </span>
                         )
                    }

               </p>

          </>
     )
};
