
import React, {useEffect, useRef} from "react";
import * as d3 from "d3";

import { MediaQuery } from '../../MediaQuery';
import { to24hours } from "../dashboard/helpers/to24hours";

export const PlotGantt = ({data, jornada}) => {

     const { _xl, _lg } = MediaQuery();

     const svgAxis = useRef(null);
     const svgPlot = useRef(null);

     useEffect( () => {
          plot( data, jornada, svgAxis, svgPlot, _xl, _lg)
     },[data, jornada, svgAxis, svgPlot, _xl, _lg]) ;

     return(
          <div>
               <svg ref={svgAxis} width={'100%'} height={_xl?'45':_lg?'45':'45'}/>      
              
               <svg ref={svgPlot} width={'100%'}  />      
          </div>
     );
};

const plot = (data, jornada, svgAxis, svgPlot, _xl, _lg) => {
     
     const dataJornada = Object.keys( data ).length > 0 
                              ? data[ jornada === 'Da' ? 'dia' : 'noche' ].filter( obj => obj.time )
                              : 0 ;
     
     const fontSizeAxis = _xl ? '16px' : _lg ? '15px': '13px';
     const fontSize = _xl ? '15px' : _lg ? '14px': '12px';
     const fontSizeLegend = _xl ? '18px' : _lg ? '18px': '15px';

     
     const margin = {
          top:30,
          right:_xl?35:_lg?25:40,
          bottom:_xl?24:_lg?21:30,
          left:_lg?30:25,
     } ;
     
     const marginPlot = {
          top: _xl ? 20: _lg? 20 :12,
          right: _xl? 40:_lg ? 35 :35,
          bottom:_xl?13:_lg?14:18,
          left:_xl?40:_lg?35:30,
     } ;

     
     const date =  (hour) => new Date( new Date().toLocaleString().split(',')[0].split('/').reverse().join('-') + ' ' + hour ) ;
     const dateTomorrow = (hour) => new Date( new Date( new Date().getTime() + 1000*60*60*24 ).toLocaleString().split(',')[0].split('/').reverse().join('-')  + ' ' + hour ) ;


     const svg = d3.select(svgAxis.current);
     const svgP = d3.select(svgPlot.current);


     svg.selectAll("*").remove(); // Clear svg content before adding new elements
     svgP.selectAll("*").remove(); // Clear svg content before adding new elements
     
     svgP.attr('height', document.getElementById('labels').offsetHeight + 30  ) ;

     const width = svgP._groups[0][0].clientWidth  ;
     const height = svgP._groups[0][0].clientHeight  ;

     const interval = [
               jornada === 'Da' ?  date('06:00:00') : date('18:00:00'),
               jornada === 'Da' ?  date('18:00:00') : dateTomorrow('06:00:00'),
          ] ;

     const xScale = d3.scaleTime()
                         .domain(
                              [
                                   interval[0] ,
                                   interval[1] 
                              ]
                         )
                         .range([ marginPlot.left, width - marginPlot.right ])
                         .nice() ;

     const intervalY = data?.dia || data?.noche
                         ? data[ jornada === 'Da' ? 'dia' : 'noche' ].filter( obj => obj.time ).length 
                         : 1 ;
     
     const yScale = d3.scaleLinear()
                         .domain( [ 0, intervalY ] )
                         .range([marginPlot.top, height - marginPlot.bottom ]) ;

     // Insercion del eje x Dia
     svg.append("g")
          .attr("transform", `translate(0, ${margin.top})`)
          .call( d3.axisTop(xScale).ticks(10).tickSize(0) )
          .call( g => g.select(".domain").remove() )
          .attr("color", 'black' )
          .attr('opacity','0.8')
          .attr("font-weight",'400')
          .selectAll("text")
          .attr('style', `font-size:${fontSizeAxis}; font-family: Montserrat !important`) ;
     

     
     // Lineas punteadas
     svgP.append('g')
          .selectAll('line')
          .data( dataJornada )
          .enter()
          .append('line')
          .attr( 'x1', 0 )//d => xScale( date( to24hours( interval[0] ) ) ) ) //xScale( date( to24hours( obj.time ) ) ) )
          .attr( 'y1', (_,i) =>  yScale(i) )
          .attr( 'x2', width - (_xl?15:_lg?10:20)) //d => xScale( date( to24hours( interval[1] ) ) ) )
          .attr( 'y2', (_,i) =>  yScale(i) )
          .attr( 'stroke-width', _lg? 2.3 :2 )
          .attr( 'stroke', 'rgba(139, 137, 137, 0.87)' )
          .attr( 'stroke-linecap', "round")
          .attr( 'stroke-dasharray', "4 5")
          .attr( "opacity", 0.75 ) ;

     // Cuadros
     svgP.append('g')
          .selectAll('rect')
          .data( dataJornada )
          .enter()
          .append("rect")
          .attr("height", _xl? 25:_lg? 20:18 )
          .attr("width", _xl ? 75 :_lg? 70  : 60 )
          .attr('x', d => xScale( date( to24hours( d.time ) ) ) - (_xl ? 73/2: _lg? 70/2: 60/2) )
          .attr('y', (_,i) => yScale(i) - (_xl?25/2: _lg ? 20/2 :18/2) )
          .attr('rx', 6 )
          .attr('ry', 6 )
          .attr("fill", 'rgba(9, 155, 189, 1)' )
          // .attr('stroke', 'cadetblue')
          .attr('stroke-width', 2) ;

     // texto
     svgP.append('g')
          .selectAll('text')
          .data( dataJornada )
          .enter()
          .append('text')
          .text( d => `${d.interval.replace(/[ m]/g,'')} min`)
          .attr('x', d => xScale( date( to24hours( d.time ) ) ) - parseInt(fontSize.replace(/px/g,''))*1.7 )
          .attr('y', (_,i) => yScale(i) + 4 )
          .attr("text-anchor", "start")
          .attr("alignment-baseline", "middle")
          .attr("font-family", "Montserrat")
          .attr("font-size", fontSize)
          .attr("font-weight",'bold')
          .attr("fill", 'white' )
          .on("mouseover", (_,d) => {
               svgP.select('#textLegend').text( `${d.label} - ${d.time} - ${d.interval}` )
          })
          .on("mouseout", () => {
               svgP.select('#textLegend').text( '' )
          })
     
     // texto marca
     svgP.append('text')
          .attr('id','textLegend')
          // .text( 'Mario')
          .attr('x', width/2  )
          .attr('y', height - marginPlot.bottom  )
          .attr("text-anchor", "middle")
          .attr("alignment-baseline", "middle")
          .attr("font-family", "Montserrat")
          .attr("font-size", fontSizeLegend)
          .attr("font-weight",'bold')
          .attr("fill", 'rgba(0, 91, 165, 1)' ); 

}