
import { to24hours } from '../dashboard/helpers/to24hours' ;

import { getLastHour }  from '../helpers/getLastHour';

export const difHoraria = () => {

     const hourServer = getLastHour('lastHour') ;
     const hourLocale = new Date().toLocaleString();

     return parseInt( hourServer.split('T')[1].split(':')[0] - to24hours( hourLocale.split(', ')[1] +'' ).split(':')[0]+'' )
     // hourLocale.split(',')[1].split(':')[0] )
};
