import React, { useEffect } from 'react';
import { Box, Grid } from '@mui/material';
import { Redirect } from 'react-router-dom';
// https://cocv3.cysce.com/coc/panel/uci/interface1

//Componentes
import { HistoriaClinica } from './components/monitorPaciente/HistoriaClinica';
import { InformacionClinica } from './components/monitorPaciente/InformacionClinica';
import { TabInfoClinica } from './components/monitorPaciente/TabInfoClinica';
import { Balance } from './components/monitorPaciente/Balance';
import { Examenes } from './components/monitorPaciente/Examenes';
import { Alertas } from './components/monitorPaciente/Alertas';
import { Tabs } from './components/monitorPaciente/Tabs';
import { Dashboard } from './components/dashboard/Dashboard';
import { Laboratorio } from './components/laboratorio/Laboratorio';
import { Imagenes } from './components/imagenes/Imagenes';
// import { Respiratorio } from './components/respiratorio/Respiratorio';
import { Respiratorio } from './components/respiratorio/Respiratorio';
import { Enfermeria } from './components/enfermera/Enfermeria';
import { BalanceTab } from './components/balance/BalanceTab';
import { HistoriaTab } from './components/historia/HistoriaTab';
import { Stl } from './Stl';
import { TimeLine }  from './components/timeLine/TimeLine';

import { Title } from './components/elements/Title' ;

// Informacion del formulario
import { getFromServer } from '../uciTablet/recursosFHIR/getFromServer' ;

// store
import { useDispatch } from 'react-redux'
import { assignId } from '../../redux/patientSlice'
import { setToken } from './components/helpers/token';



const getDataFormulario = async() => {
     console.log( "interface:", JSON.parse( localStorage.getItem('idPatient') ) )
     const { data } = await getFromServer( JSON.parse( localStorage.getItem('idPatient') ) ) ;
     localStorage.setItem('Data', JSON.stringify( data, null, 2) ) ;
};

const Monitor = () => {
     //console.clear();
     const dispatch = useDispatch();

     const classes = Stl() ;

     const { timeLine, dayNow } = TimeLine() ;

     const {id} = JSON.parse( localStorage.getItem('idPatient') );

     useEffect( ()=>{
          getDataFormulario();
          dispatch( assignId(id) );
          setToken();
     }) ;


     const tabArray = [
          {obj:<Dashboard dt={[ id, dayNow ]}/>},
          {obj:<Laboratorio  />},
          {obj:<Imagenes  />},
          {obj:<Respiratorio dt= {[ id, dayNow ]} />},
          {obj:<Enfermeria dt= {[ id, dayNow ]} />},
          {obj:<BalanceTab dt={[ id, dayNow ]}/>},
          {obj:<HistoriaTab dt={[ id, dayNow ]} />}
     ];

     const { tabObj, tabIndex } = Tabs(Stl);

     document.getElementById("root").style.backgroundColor='#d1e1e5' ;

     return (

          <div
               id="interface"
               style={{ width: '100%' }}
          >
               {
                    id===1
                         ?
                              <Redirect to="/coc/panel/uci/home" />
                         :
                              <span/>
               }
               <Box
                    className={ classes.bg_blue }
               >
                    <Title color={'white'}>
                         Perfil Paciente
                    </Title>
               </Box>

               <Grid
                container
                direction="row"
                justifyContent="space-evenly"
                spacing={1}
              >

                    {/* Columna izquierda */}
                    <Grid item xs={3} >
                         <Box sx={{pr:1,py:1}}>

                              <HistoriaClinica  data={[ id ]}  stl={classes}  />

                              <InformacionClinica  data={[ id ]} stl={classes} />

                              <TabInfoClinica data={[ id, dayNow ]}  stl={classes}/>

                              <Balance data={[ id, dayNow ]} stl={classes}/>

                              <Box mt={1}>
                                   <Grid container spacing={2} >

                                   <Grid item xs={6} >
                                        <Examenes  data={[ id, dayNow ]} stl={classes} />
                                   </Grid>

                                   <Grid item xs={6} >
                                        <Alertas data={[ id, dayNow ]} stl={classes}/>
                                   </Grid>

                                   </Grid>
                              </Box>

                         </Box>
                    </Grid>

                    {/* Columna Derecha */}
                    <Grid item xs={9}>
                         <Box lg={{pl:1}} >

                              { tabObj() }

                              { timeLine() }

                              {
                                  tabArray[tabIndex].obj
                              }

                         </Box>
                    </Grid>
               </Grid>
          </div>
     );
};

export default Monitor;
