
import React, { useEffect, useRef } from 'react';
import * as d3 from "d3";

import { MediaQuery } from '../../MediaQuery';
import { unitToTwoUnit } from '../helpers/unitToTwoUnit';
import { difHoraria } from './difHoraria';

const Plot = ( _xl, _lg, svgSindro, setHour, dayNow ) => {

     const fontSize =  _lg ? '14px': '12px' ;
     const color_fill = 'rgba(0, 91, 165, 1)';


     const diferencia = difHoraria();
     const dateFromTimeLine = new Date(`${dayNow[2]}-${unitToTwoUnit(dayNow[1])}-${unitToTwoUnit(dayNow[0])}${new Date().toLocaleString().split(',')[1].split(':')[0]}:00:00`)
     const date =  (hour) => new Date( new Date(dateFromTimeLine).toLocaleString().split(',')[0].split('/').reverse().join('-') + hour ) ;

     const margin = {
               right:_lg ? 24 : 23,
               top:5,
               bottom: 15,
               left: _lg ? 18 : 15,
               m:5
          } ;

     const svgEl = d3.select(svgSindro.current);
     svgEl.selectAll("*").remove(); // Clear svg content before adding new elements

     const width = svgEl._groups[0][0].clientWidth  ;
     const height = svgEl._groups[0][0].clientHeight  ;

     const svg = svgEl.append("g")
                    .attr("width", `${width }`)
                    .attr("height",`${height-margin.top}`) ;

     const  xScale = d3.scaleTime()
                         .domain(
                              [
                                   date( ' 00:00:00' ) ,
                                   date( ' 23:59:00' )
                              ]
                         )
                         .range([margin.left + margin.m , width-margin.right ])
                         .nice() ;


     // Insercion del eje x Dia
     // [
     //      d3.axisBottom(xScaleDia).ticks(6), // Insercion del eje xDia
     //      d3.axisBottom(xScaleNoche).ticks(3)  // Insercion del eje xNoche
     // ].map( scale =>
     //      svg.append("g")
     //           .attr("transform", `translate(0, ${margin.bottom})`)
     //           .call( scale )
     //           .attr("font-size", fontSize)
     //           .attr("color", color_fill )
     //           .attr('opacity','0.95')
     //           .attr("stroke-width", 2.7)
     //           .selectAll("text")
     //           .attr("transform", "translate(0,3)")
     //           .attr('style', `font-size:${fontSize}; font-family: Montserrat !important`)
     //           .attr('id', (d,i) =>`id_${new Date(d).toLocaleString()
     //                               .split(' ')[1]
     //                               .split(':')[0]}-${i}`
     //           )
     // );
     //
     // d3.select('#id_12-6').remove(); // Borrar la ultima marca del xScaleDia

     svg.append("g")
         .attr("transform", `translate(0, ${margin.bottom})`)
         .call( d3.axisBottom(xScale) )
         .attr("font-size", fontSize)
         .attr("color", color_fill )
         .attr('opacity','0.95')
         .attr("stroke-width", 2.7)
         .selectAll("text")
         .attr("transform", "translate(0,3)")
         .attr('style', `font-size:${fontSize}; font-family: Montserrat !important`)
         .attr('id', (d,i) =>`id_${new Date(d).toLocaleString()
                             .split(' ')[1]
                             .split(':')[0]}-${i}`
         );

     const dataHours = [...Array(24)]
                         .map( (_,i)=>  parseInt(i)<10 ? `0${i}` : `${i}` )
                         .map( h => new Date( new Date(dateFromTimeLine).toLocaleString().split(',')[0].split('/').reverse().join('-')+` ${h}:00:00` ) );

     const flagDate = (d) => d <= new Date() ;


     // circulos
     svg.append('g')
          .selectAll("circle")
          .data( dataHours )
          .enter()
          .append("circle")
          .attr('id', (d,i) => `circ${i}` )
          .attr("fill", d => flagDate(d) ? color_fill : 'white' )
          .attr("stroke", d => flagDate(d) ? "none": color_fill )
          .attr("stroke-width", 1.7 )
          .attr("cx", d => xScale(d) )
          .attr("cy", d => height/2 - (_lg?9:7) )
          .attr("r", _lg ? 9 : 6)
          .attr("cursor", d => flagDate(d) ? 'pointer' : 'default' )
          .on( "mouseout", e => d3.select(`#${e.originalTarget.id}` )
                                   .attr('stroke', color_fill )
                                   .attr("stroke-width", 1.7 )
                                   .attr('r', _lg ? 9 : 6 )
                              )
          .on( "mouseover", (e,d) => d3.select(`#${e.originalTarget.id}` )
                                   .attr('stroke', ()=> flagDate(d) ? "#09B3A5" : color_fill )
                                   .attr("stroke-width", ()=> flagDate(d) ? 4 : 1.7 )
                                   .attr('r', ()=> flagDate(d) ? _lg ? 10 : 6 : _lg ? 9 : 6 )
                              )
          .on("mousedown", (e,d) => {
               const time = new Date( d.getTime() + 1000*60*60*diferencia ) ;
               if( flagDate(d) ){
                    setHour( `T${ unitToTwoUnit( time.getHours() ) }:00:00Z` )
               }
          })


} ;

export const TimeLine = ({setHour, dayNow}) => {

     const {_xl, _lg} = MediaQuery() ;

     const svgSindro = useRef(null);

     useEffect( ()=> {
          Plot( _xl, _lg, svgSindro, setHour, dayNow );
     },[ _xl, _lg, svgSindro, setHour, dayNow ]);

     const styleSvg = {
          border: '2px solid hsl(242, 0%, 35%)',
          borderRadius: '0.2rem',
          paddingBlock: '0.3rem'
     }

     return(
          <svg
               ref={svgSindro}
               width={'100%'}
               height={ _lg ? '55px' : '50px' }
               style={styleSvg}
          />
     );
};
