

export const to24hours = ( hour12 ) => { //"10:00 am","10:00am","09:30PM"

     const time = hour12.replace(/[^0-9:]/g,'').split(':') ;
     const jrnd = hour12.toLowerCase().replace(/[\s+:.0-9]/g,'')

     const addNum = jrnd.indexOf('pm') >= 0
                         ? time.indexOf('12') >= 0
                              ? 0
                              : 12
                         : time.indexOf('12') >= 0
                              ? -12
                              : 0 ;

     return( `${ parseFloat(time[0]) + addNum }:${ time[1] }`) ;

};
