
// rutina empleada por <getData> en Ventilacion.js y Respiratorio.js

// Retorna tendencia, promedio y vector de datos.

export const processValueDate = ( data ) => {

     //Tendencia como el promedio de la pendiente entre los ultimos 5 puntos:

     const tendencia = ( array ) => {

          const points = array.slice( data.length - 5 ).reverse()

          return(
               ( points[2][0] - points[0][0] )*1000*60*60 /
               parseFloat( new Date(points[2][1]).getTime() - new Date(points[0][1]).getTime() )
          );
     } ;

     const promedio = data.reduce( (sum, item, idx) => sum + (!parseFloat(item[0]) ? sum/(idx+1) : parseFloat(item[0])) , 0 )/data.length ;

     const ultValue = data.reverse()[ data.length-1 ][0] ;
     // const ultValue = data.slice( data.length - 5 ).reverse().map( num => parseFloat(num).toFixed(3))

     const fixed = (num) => {
          return(
               num > 1
               ? parseFloat(num).toFixed(1)
               : parseFloat(num).toFixed(3)
          )
     };
     return(
          [
               fixed(data ? tendencia(data) : 0),
               fixed(data ? promedio : 0),
               fixed(data ? ultValue : 0),
               [
                    ...data.slice( 0 ).reverse()
               ]
          ]
     );
};
