import React, {useEffect, useState, useMemo } from "react";
import * as d3 from "d3";
import { MediaQuery } from '../../MediaQuery';


// Obtener la fecha
const getHoMiSe = ( stringDate , ms=0 ) => {
     const millis = new Date(stringDate).getTime() + ms ;
     return new Date(millis).toLocaleString("en-es").split(',')[1] ;
};

export const PlotFlowRespiratorio = ( {data, name, point=80, setPoint, len=200, setLen, color} ) => {

     const yData = data.body[0].data.data ; // Numeros : [ [ "13.3", "13.6", "13.8",  ], [ "13.2", "13.2", "13.3",  ], ... ]
     const xData = data.body[0].data.date.reverse() ; // fechas : ["2022-02-24 19:08:49.399955", ... ]
     const delay = data.body[0].data.delay ; // intervalo de tiempo en ms de cada epoch : yData[i]  :["342740", ... ] ms

     const svgSindro = React.useRef(null);

     const [ rect, setRect ] = useState({
          x:0,
          width:0
     });

     const {_xl,_lg} = MediaQuery();

     const datum = useMemo( () =>{
          const makeData = () => {

               let datum = [];
               let deltaTime = 0;
               // console.log( "make data" );
               yData.forEach( (array,idxArray)=>{

                    let timeGet = new Date( xData[idxArray+1] ).getTime()-new Date( xData[idxArray] ).getTime()  ;
                    let flagZero = false ;

                    if(delay[idxArray] > timeGet ){
                         deltaTime = timeGet  / array.length
                    }else{
                         deltaTime = delay[idxArray] / array.length ;
                         flagZero = true ;
                    };

                    datum = datum.concat(
                         array.map( (item,idxItem)=>{
                              if( (flagZero && idxItem === 0) || (flagZero && idxItem === array.length-1) ){
                                   return [ new Date( xData[idxArray] ).getTime() + deltaTime*idxItem, 0 ]
                              }else{
                                   return [ new Date( xData[idxArray] ).getTime() + deltaTime*idxItem, item ]
                              }
                         })
                    );
               });

               return( datum )
          };

          return(
               makeData(yData,xData,delay)
          )

     },[yData,xData,delay] ) ;

     useEffect( () => {

          plot(svgSindro, datum, name, point, setPoint, len, setLen, rect, setRect,_xl,_lg, color)

     },[svgSindro, datum, name, point, setPoint, len, setLen, rect, setRect, _xl, _lg, color])

     return(
          <svg ref={svgSindro} width={'100%'} height={(_xl?'240':_lg ?'190': '180')} />
     );
};



const plot = (svgSindro, datum, namePlot, point, setPoint, len, setLen, rect, setRect,_xl,_lg, color) => {

     const svgEl = d3.select(svgSindro.current);
     svgEl.selectAll("*").remove(); // Clear svg content before adding new elements

     const width = svgEl._groups[0][0].clientWidth  ;
     const height = svgEl._groups[0][0].clientHeight  ;

     const fontSize = _xl ? '22px' : _lg ? '16px': '12px' ;
     const fontTitle = _xl ? '22px' : _lg ? '16px': '12px' ;

     const leftSize = d3.extent( datum , d => d[1].length )  ;

     const margin = {
          top:    _xl ? 22 : _lg ? 22 : 20,
          right:  _xl ? 30 : _lg ? 25 : 20,
          bottom: _xl ? 30 : _lg ? 22 : 35,
          left: (fontSize.split('px')[0]*leftSize[1]*0.55)
     } ;

     let pr0 = datum.length*(point)/100, pr1 = pr0;

     if( (pr0 - len/2) < 0 ){
          pr0 = 0 ;
          pr1 = len ;
     }else if( (pr0 + len/2) >  datum.length){
          pr1 = datum.length -1;
          pr0 = pr1 - len ;
     }else{
          pr0 = pr0 - len/2 ;
          pr1 = pr1 + len/2 ;
     };

     const part = [ parseInt(pr0), parseInt(pr1) ]  ;

     const svg = svgEl.append("g")
                    .attr("width", `${width }`)
                    .attr("height",`${height}`);


     const backColor = 'white' ;
     // fondo de grafica
     svg.append('g')
          .append('rect')
          .attr('x', margin.left)
          .attr('y', margin.top)
          .attr('width', width - margin.right - margin.left)
          .attr('height', height - margin.top - margin.bottom)
          .attr('stroke', 'rgba(0, 0, 0, 0)')
          .attr('fill', backColor )

     const xScale = d3.scaleTime()
                    .domain( [
                         new Date( datum[ part[0] ][0] ),
                         new Date( datum[ part[1] ][0] )
                    ]) // eje en segundos getHoMiSe(xData[0], delay[0])
                    .range([margin.left, width-margin.right] )
                    // .nice() ;

     const x_axis = d3.axisBottom()
                    .scale(xScale)
                    .ticks(_xl|_lg ? 8 : 5)


     // Insercion del eje x
     svg.append("g")
          .attr("transform", `translate(0, ${height-margin.bottom})`)
          .call(x_axis)
          .attr("font-family", "Montserrat")
          .attr("font-size", fontSize )
          .attr("color", 'black' )
          .attr('opacity','0.9')
          .selectAll("text")
          // .attr("transform","rotate(45,-10,15)" ) ;

     // Eje vertical
     const limits = d3.extent( datum.slice( part[0], part[1]) , d => parseFloat(d[1]) ).reverse() ;

     const yScale = d3.scaleLinear()
          .domain( [limits[0], limits[1] ] )
          .range([margin.top , height - margin.bottom ]);

     const yAxis = d3.axisLeft()
                    .scale(yScale)
                    .tickSize(-width + margin.right + margin.left )
                    .ticks(_xl|_lg ? 3 : 4)

     // Insercion del eje y
     svg.append("g")
          .attr("transform", `translate(${margin.left} ,0)`)
          .call(yAxis)
          .attr("font-family", "Montserrat")
          .attr("font-size", fontSize )
          .attr("color", 'black' )
          .attr('opacity','0.9')
          .call(g => g.select(".domain") // Quita la primera linea continua y la linea vertical continua
               .remove()
          )
          .call(g => g.selectAll("line") // .tick:not(:first-of-type)
               .attr("stroke-opacity", "0.5")
               .attr("stroke-dasharray", "2,2")
          )




     // https://stackoverflow.com/questions/39023154/how-to-make-a-color-gradient-bar-using-d3js
     var lg = svg.append('linearGradient')
      .attr('id', `Gradient_${color}`)
      .attr('x1', 0)
      .attr('x2', 0)
      .attr('y1', 0)
      .attr('y2', 1);

     lg.append('stop')
      .attr('offset', '0%')
      .attr('stop-color', `${color}45` ) ;

     lg.append('stop')
      .attr('offset', '100%')
      .attr('stop-color', backColor );

     // Datos:
     // Linea
     svg.append("path")
          .datum( datum.slice( part[0], part[1] ) )
          .attr("fill", `url(#Gradient_${color})` )//"#cce5df" )
          .attr("stroke", color ) //"rgba(209, 19, 19, 1)") //color_fill)
          .attr("stroke-width", 1)
          .attr("d", d3.area()//d3.line()
               .x( d => xScale( d[0] ) )
               .y0( d =>  {
                    if( yScale( 0 ) <= height -margin.bottom ){
                        return yScale( 0 )
                    }
                    return  height -margin.bottom
               })
               .y1( d => yScale(parseFloat(d[1])) )
               .curve( d3.curveMonotoneX )
          );

     // Titulo de la grafica
     svg.append("g")
          .append("text")
          .attr("x", width/2 - namePlot.length*fontTitle.split('px')[0]*0.23  )
          .attr("y", (_xl? 16:15))
          .attr("font-family", "Montserrat")
          .attr("font-size", fontTitle )
          .attr("font-weight", "500" )
          .attr("line-height", "16px" )
          .text( namePlot ) ;

     // tiempos de marca
     [
          [ margin.left, getHoMiSe( new Date( datum[part[0]][0] ) ) ], // hora del registro inicio
          [ width - 7*fontSize.split('px')[0] , getHoMiSe(new Date( datum[part[1]][0] ) ) ] // hora del registro inicio
     ].forEach( item => {
          svg.append("g")
               .append("text")
               .attr("x", item[0] )
               .attr("y", (_xl?20:_lg?16:15) )
               .attr("font-family", "Montserrat")
               .attr("font-size", fontSize )
               .attr("font-weight", "500" )
               .attr("line-height", "16px" )
               .text( item[1] )
     });

     //  rectangulo del zoom
     svg.append('g')
          .append('rect')
          .attr('x', rect.x )
          .attr('y', margin.top )
          .attr('width', rect.width )
          .attr('height', height - margin.top - margin.bottom)
          .attr('fill', 'rgba(0, 0, 150, 0.4)')


     // eventos del mouse
     svg.append('g')
          .append('rect')
          .attr('id', `rect${namePlot}`)
          .attr('x', margin.left)
          .attr('y', margin.top)
          .attr('width', width - margin.right - margin.left)
          .attr('height', height - margin.top - margin.bottom)
          .attr('stroke', 'rgba(0, 0, 0, 0)')
          .attr('fill', 'rgba(0, 0, 25, 0.01)')
          .attr('style','cursor:pointer')
          .on('wheel', ( event ) => wheel(event) );

     // entrar la posicion de indexado del dato ms cercano al valor dado.
     // const bisect = d3.bisector( (d) => return d[0] ).left ;

     const wheel = ( event ) => {
          if ( event.shiftKey ) {

               // let offSet = window.document.getElementById(event.srcElement.id).nearestViewportElement.parentElement.offsetLeft
               // let layerX = event.clientX - offSet
               // let nPos = bisect( datum.slice( part[0], part[1] ) , xScale.invert( layerX ).getTime() ) ;
               // let nPos = datum.slice( part[0], part[1] ).findIndex( d => d[0] > xScale.invert( layerX ).getTime() ) ;

               let deltaLen = len + Math.sign(event.wheelDeltaY)*(part[1]-part[0])/5 ;

               if( (deltaLen > 12) & (deltaLen < 1000) ){

                    setLen(deltaLen);

                    // if( nPos != 0){
                    //      setPoint({value:(nPos+part[0]) / datum.length*100}  )
                    // }
               };
          };
     };

     const mouseOut = () => {
          // console.log(" mouse out") ;
          // console.log("Dimensiones", rect.x, rect.x+rect.width );

          if( rect.width > 0){

               let p1 = datum.findIndex( item => item[0] >= xScale.invert(rect.x).getTime() ) ;
               let p2 = datum.findIndex( item => item[0] >= xScale.invert(rect.x+rect.width).getTime() ) ;


               if( p1 !== 0 & p2 !== 0 & p2-p1 > 12 ){
                    setPoint( {value:(p2+p1)/2/datum.length*100 })
                    setLen(p2-p1);
               }

               setRect({ x:0, width:0 });

          }
     };

     let dx = 0, flag = true, movX = 0 ;
     const dragHandler = d3.drag().on( 'drag', (event) => {
          let p0 = 0;

          if( part[1]-part[0] > 30 ){

               if( flag ){
                    rect.x = event.x ;
                    movX = event.x ;
                    flag = false ;
               }

               if( Math.abs(event.dx) < 200 ){
                    dx = dx + event.dx
                    movX = movX + event.dx
               }else{
                    dx = dx +  event.dx/500
               };

               if( dx < 0 & (rect.x > 0)){
                    rect.x = movX ;
                    p0 = p0 + dx*(-1) ;
               }else{
                    p0 = p0 + dx ;
               };

               if( (p0 > 0) & ( rect.x+p0 < width - margin.right )  ){
                    rect.width = p0 ;
                    setRect({...rect});
               };
          }
     }).on('end',()=>mouseOut());

     dragHandler( svg.select(`#rect${namePlot}`) );

};
