import React,{useEffect} from "react";

import * as d3 from "d3";
import { MediaQuery } from '../../MediaQuery';
import { unitToTwoUnit } from '../helpers/unitToTwoUnit'

export const PlotTimeLine =({ DateNow, set })=>{

     const { _xl, _lg } = MediaQuery() ;

     const svgRef = React.useRef(null);
     const margin = {top:2, right:(_xl?13:10), bottom: _xl?20:15, left: 10 } ;

     useEffect(() => {

          const svgEl = d3.select(svgRef.current);
          svgEl.selectAll("*").remove(); // Clear svg content before adding new elements

          const width  = svgEl._groups[0][0].clientWidth  ;
          const height = svgEl._groups[0][0].clientHeight ;

          const svg = svgEl.append("g")
                         .attr("width", `${width}`)
                         .attr("height",`${height}`)

          const arrowD = "M4.52043 19.2962L15.0835 11.0951C15.3592 10.8814 15.5766 10.6221 15.7215 10.3343C15.8663 10.0464 15.9353 9.73664 15.9237 9.42531C15.9122 9.11399 15.8206 8.80821 15.6547 8.52815C15.4889 8.24809 15.2527 8.00011 14.9617 7.80059L4.39863 0.56077C4.00731 0.292231 3.53088 0.12098 3.02532 0.0671116C2.51977 0.0132435 2.00604 0.0789908 1.54434 0.256639C1.08264 0.434287 0.692123 0.716471 0.41866 1.07008C0.145197 1.42369 0.000163579 1.83407 0.000451691 2.25307V17.654C-0.00923542 18.0877 0.137083 18.5141 0.42104 18.8797C0.704997 19.2453 1.11399 19.5339 1.59669 19.7093C2.0794 19.8847 2.61435 19.939 3.13449 19.8655C3.65462 19.7921 4.1367 19.594 4.52043 19.2962Z" ;

          const arrowI = "M11.4046 0.600851L0.841533 8.81199C0.565812 9.02567 0.348356 9.28497 0.203511 9.57279C0.058666 9.86061 -0.010276 10.1704 0.00123736 10.4817C0.0127507 10.7931 0.104456 11.0988 0.270302 11.3789C0.436147 11.659 0.672364 11.9069 0.963365 12.1065L11.5264 19.2962C11.9137 19.5622 12.3846 19.7329 12.8847 19.7886C13.3849 19.8444 13.894 19.7829 14.3535 19.6113C14.8131 19.4397 15.2043 19.165 15.4823 18.8187C15.7602 18.4725 15.9135 18.0687 15.9246 17.654V2.20302C15.9246 1.77274 15.7713 1.35187 15.4836 0.992374C15.1959 0.632881 14.7864 0.350505 14.3057 0.180107C13.825 0.00970898 13.2941 -0.0412552 12.7785 0.0335153C12.2629 0.108286 11.7853 0.305517 11.4046 0.600851Z";

          const meses = ( numMesUTC ) =>{
               switch (numMesUTC+1) {
                    case 1:
                         return 'Enero'
                    case 2:
                         return 'Febrero'
                    case 3:
                         return 'Marzo'
                    case 4:
                         return 'Abril'
                    case 5:
                         return 'Mayo'
                    case 6:
                         return 'Junio'
                    case 7:
                         return 'Julio'
                    case 8:
                         return 'Agosto'
                    case 9:
                         return 'Septiembre'
                    case 10:
                         return 'Octubre'
                    case 11:
                         return 'Noviembre'
                    case 12:
                         return 'Diciembre'
                    default:

               }
          }
          const azul = '#005BA5' ;
          const gris = '#B5DCE5' ;
          const radio = _xl ? 22 : _lg ? 16 : 12
          const formatDay = d3.timeFormat("%e");
          const middle_y = 2*height/3 ;

          const ms_day = 86400000 ;
          let dateNow = new Date( DateNow[2], DateNow[1]-1, DateNow[0])

          const limIzq = dateNow.getTime()-ms_day*3 ;
          const limDer = dateNow.getTime()+ms_day*3 ;

          const xScale = d3.scaleLinear()
                         .domain( [ limIzq  , limDer ] )
                         .range( [margin.left, width-margin.right] );

		// Construccion del eje
          svg.append('line')
                    .attr('id','line')
		  		.attr('x1', margin.left )
				.attr('y1', middle_y )
				.attr('x2', width-margin.right )
				.attr('y2', middle_y )
				.style('stroke','#AFC3EC')
				.style('stroke-width', _xl?4:_lg?3:2)

		// Flechas
    const cry = _xl ? 2 :_lg ? 1.7 : 1
    const crx = _xl ? 1.8 :_lg ? 1.5 : 1
		svg.append('path')
      .attr('id','arrowD')
			.attr('d',arrowD )
			.attr('fill', azul )
			.attr('transform', `translate( ${width-margin.right-15*crx }, ${middle_y - 9*cry }) scale(${cry})`)
               .on( 'mouseover',()=> d3.select('#arrowD').style('cursor','pointer'))
               .on( 'mousedown',()=>{
                    dateNow.setTime( dateNow.getTime() + ms_day )
                    set({ fecha:[dateNow.getUTCDate(), dateNow.getUTCMonth()+1, dateNow.getUTCFullYear()] })
               })
               .on('mouseout', () => {  d3.select(`#cir${2}`).style( 'fill', azul )
                                   })

		svg.append('path')
               .attr('id','arrowI')
			.attr('d',arrowI )
			.attr('fill', azul )
			.attr('transform', `translate( ${margin.left}, ${middle_y - 9*cry }) scale(${cry})`)
               .on( 'mouseover',()=> d3.select('#arrowI').style('cursor','pointer'))
               .on( 'mousedown',()=>{
                    dateNow.setTime( dateNow.getTime() - ms_day )
                    set({ fecha:[dateNow.getUTCDate(), dateNow.getUTCMonth()+1, dateNow.getUTCFullYear()] })
               })
               .on('mouseout', () => {  d3.select(`#cir${2}`).style( 'fill', azul )
                                   })

          //Elementos de marca
          const cir =  ( date, idx ) => {
                    svg.append('circle')
                         .attr('id',`cir${idx}`)
                         .attr('cx', xScale(date.getTime()) )
                         .attr('cy', middle_y )
                         .attr('r', radio )
                         .style('fill', gris)
          };

          const cirSup =  ( date, idx ) => {
                    svg.append('circle')
                         .attr('id',`cirSup${idx}`)
                         .attr('cx', xScale(date.getTime()) )
                         .attr('cy', middle_y )
                         .attr('r', radio )
                         .style('fill', 'transparent' )
                         .on('mouseover',() => {  d3.select(`#cirSup${idx}`).style( 'cursor','pointer')
                                                  d3.select(`#cir${idx}`).style( 'fill', '#46a7b8' )
                                                  d3.select(`#text${idx}`).style( 'fill', 'white' );
                                             })
                         .on('mouseout', () => {  d3.select(`#cir${idx}`).style( 'fill', gris )
                                                  d3.select(`#text${idx}`).style( 'fill', azul );
                                                  d3.select(`#cir${2}`).style( 'fill', azul );
                                                  d3.select(`#text${2}`).style( 'fill', 'white' );
                                             })
                         .on('mousedown',() => {
                              set({ fecha:[date.getUTCDate(), date.getUTCMonth()+1, date.getUTCFullYear()] })
                         });
          };

          const offSetY = _xl?5:4 ;
          const text = ( date,idx ) => {
                    svg.append('text')
                         .attr('id',`text${idx}`)
                         .attr('x', xScale(date.getTime()) - `${ formatDay( date ) }`.length*(_xl?7:_lg?5:4) )
                         .attr('y', middle_y+offSetY )
                         .attr('fill', azul )
                         .text(`${unitToTwoUnit( formatDay( date ) )}`)
                         .style('font-size', _xl ? '22px' : _lg ? '16px': '12px' )
                         .style('lineHeight', _xl ? '1.5rem' : _lg ? '1.2rem': '0.8rem' )
                         .style('font-weight', 'bold')

          };

          const mark = ( date, idx )=>{
               cir(date,idx);
               text(date,idx);
               cirSup(date,idx);
          }


          // Desplegado de marcas
          [-2,-1,0,1,2].forEach( (val,idx) =>{
                    setTimeout( ()=>{
                         d3.select(`#cir${2}`).style('fill', azul )
                         d3.select(`#text${2}`).style( 'fill', 'white' )
                    },200)
                    let d = new Date( dateNow );
                    d.setTime( dateNow.getTime() + ms_day*val )

                    return(
                         mark( d, idx )
                    )

          })

          // Nombre de los meses
          const mesNow = meses( dateNow.getUTCMonth() ) ;

          let day = new Date( dateNow );
          day.setTime( dateNow.getTime() + ms_day )
          const mesTom = meses( day.getUTCMonth( )  ) ;

          day.setTime( dateNow.getTime() - ms_day )
          const mesAft = meses( day.getUTCMonth( )  ) ;

          const flag = mesNow===mesTom && mesNow===mesAft

          const offSetTextY = _xl ? 35 : _lg ? 30: 25 ;
          const offSetTextX = _lg ? (flag ? mesNow.toString().length*6 : mesTom.toString().length*5 ): (flag ? mesNow.toString().length*4 : mesTom.toString().length*4 )

          // mes Actual
          svg.append('text')
               .attr('x', flag ? width/2 -offSetTextX : width/4 -offSetTextX )
               .attr('y', middle_y - offSetTextY )
               .attr('fill', azul )
               .text( mesNow === mesAft ? mesNow : mesAft )
               .style('font-size', _xl ? '28px' : _lg ? '22px': '16px' )
               .style('lineHeight', _xl ? '1.5rem' : _lg ? '1.2rem': '0.8rem' )
               .style('font-weight', 'bold')

          // mes Posterior
          svg.append('text')
               .attr('x',  3*width/4 - offSetTextX )
               .attr('y', middle_y - offSetTextY )
               .attr('fill', azul )
               .text( flag ?' ':`${ mesTom }`)
               .style('font-size', _xl ? '28px' : _lg ? '22px': '16px')
               .style('lineHeight', _xl ? '1.5rem' : _lg ? '1.2rem': '0.8rem' )
               .style('font-weight', 'bold')
    })

     return(
          <svg ref={svgRef} width={'100%'} height={_xl ? '90' : _lg ? '80': '60'} />
     );
}
