import React,{useState } from 'react';
import { Box, Grid, Button, Collapse} from '@mui/material';
import { MediaQuery } from '../../MediaQuery';
import { DashContentSindro } from './DashContentSindro'

import { Field } from '../elements/Field' ;
import { Text } from '../elements/Text' ;

export const BoxSindromatico  = ({stl, title, data}) => {

     const classes= stl ;
     const { _xl, _lg } = MediaQuery();

     const [open, setOpen] = useState(false);
     const handleClick = () => {
       setOpen(!open);
     };

     const [index, setIndex] = useState({value:0});
     const handleIndex = (v) => {
          setOpen(!open);
          setIndex( {value:v} );
     }

     const ColorConvencion = () => {

          const styleName = {
               fontSize: _xl ? '22px' : _lg ? '16px': '12px',
               fontFamily:'Montserrat',
               border:'1px solid rgb(20,20,20)',
               paddingInline:'0.5rem',
               paddingBlock:'0.1rem',
               marginLeft:'0.5rem',
               borderRadius:'5px',
               fontWeight: '500',
               display: 'grid',
               justifyItems: 'stretch',
               textAlign: 'center'
          };
          
          return(

               [
                    ['Rojo', '#E9312A','Grave'],
                    ['Amarillo', '#FFFD67','Atencin'],
                    ['Naranja', '#F1A147','Anormal'],
                    ['Verde', '#53AC62','Normal']
               ].map( (renglon, idx) => {

                    return(
                         <div key={idx} style={{marginBlock:'0.5rem',width:'100%'}}>
                              <span style={{...styleName, background:renglon[1]}}>
                                   {renglon[0]} : {renglon[2]}
                              </span>
                         </div>
                    )
               })
          )
     }

     return (
          <Box
               mt={1}
               className={classes.bg_white}

          >
               <Grid
                    container
               >
                    <Grid
                         item
                         xs={9}
                    >

                    <Box
                        className={[classes.bg_blue].join(' ')}
                        py={1} pl={1}
                    >
                        <Field color='white'>
                            {title}
                        </Field>
                    </Box>

                    </Grid>
                    <Grid
                         item
                         xs={3}
                         className={[classes.bg_blue, classes.px_0].join(' ')}
                    >
                         <Button
                              onClick={()=>{handleClick()}}
                              className={classes.px_0}
                              fullWidth
                         >

                              <svg width="64" height={ _xl?'28':_lg?'23':'18'} viewBox="0 -2 22 21" fill="none" xmlns="http://www.w3.org/2000/svg">
                                    <path d="M0.637178 4.52043L9.32358 15.0835C9.54991 15.3592 9.82455 15.5766 10.1294 15.7215C10.4343 15.8663 10.7624 15.9353 11.0921 15.9237C11.4219 15.9122 11.7458 15.8206 12.0424 15.6547C12.339 15.4889 12.6017 15.2527 12.813 14.9617L20.4812 4.39863C20.7656 4.00731 20.947 3.53088 21.0041 3.02532C21.0611 2.51977 20.9915 2.00604 20.8033 1.54434C20.6152 1.08264 20.3163 0.692123 19.9418 0.41866C19.5672 0.145197 19.1326 0.000163579 18.6888 0.000451691L2.37658 0.000451691C1.91722 -0.00923542 1.46559 0.137083 1.07832 0.42104C0.69105 0.704997 0.385376 1.11399 0.199621 1.59669C0.0138664 2.0794 -0.0437031 2.61435 0.0341358 3.13449C0.111975 3.65462 0.321758 4.1367 0.637178 4.52043Z"
                                      fill="white"
                                    />
                              </svg>

                         </Button>
                    </Grid>
               </Grid>

               <Collapse in={open} timeout="auto" unmountOnExit>
                    {
                         data.map((value,idx)=>{
                              return(
                                   <Button
                                        fullWidth={true}
                                        key={idx}
                                        onClick={()=>{handleIndex(idx)}}
                                        className={[classes.px,  classes.font_monserrat, classes.text_color_title ].join(' ')}
                                   >
                                        <Text>
                                            {value.key}
                                        </Text>
                                   </Button>
                              )
                         })
                    }

               </Collapse>

               <Box className={[classes.px, classes.py].join(' ')}>
                    <DashContentSindro
                         stl={classes}
                         data={data[index.value].obj}
                    >
                    </DashContentSindro>

                    <ColorConvencion/>

               </Box>

          </Box>
     )
}
