import React from 'react' ;
import { styled } from '@mui/system';

import { MediaQuery } from '../../../MediaQuery' ;


const colorGround = [
     //Background, border-contents
     ['#E0F1FF','#026FCA'],
     ['#D4FFFA','#009C8A'],
     ['#C1FFE0','#008C45'],
     ['#D4FFFA','#009C8A'],
     ['#E0F1FF','#026FCA']
];

const Container = styled('div')({
     width: 'auto',
     height: 'min-content',
     borderRadius: '0.3rem',
     paddingBlock: '0.2rem',
     paddingInline: '0.5rem',
     fontFamily:'Montserrat'
});

const TitleParameter = styled('span')({
     fontWeight: '700',
});

const ValueParameter = styled('span')({
     fontWeight: '500',
});

const TitleTendencia = styled('span')({
     fontWeight: '400'
});

const ValueTendencia = styled('div')({
     fontWeight: '300',
     paddingLeft: '1.1rem'
});


// Componente
export const Square = (
     {
          title='TLP',
          value='100',
          unids='xx',
          tendencia='5',
          prom='0'
     }) => {

    const {_xl, _lg} = MediaQuery() ;

    const flexRow = {
         display: 'flex',
         flexDirection: 'row',
         justifyContent: 'space-around',
         alignItems: 'center'
    };

    const styleSquare = () => {
          const index = Math.floor( Math.random() * 5 )
          return(
               {
                    border: `0.13rem solid ${colorGround[index][1]}`,
                    background: `${colorGround[index][0]}`,
                    color: `${colorGround[index][1]}`,
                    fontSize: _xl ? '22px' : _lg ? '16px': '11px'
               }
          );
    };

     return(
          <Container
               style={styleSquare()}
          >
               <div
                    style={{width:'max-content'}}
               >
                    <TitleParameter>
                         {title}:
                    </TitleParameter>
                    <ValueParameter>
                         { ' ' + value }{ ' ' + unids }
                    </ValueParameter>
               </div>
               <div style={flexRow}>
                    <div>
                         <TitleTendencia>
                              Tendencia:
                         </TitleTendencia>
                         <ValueTendencia>
                              {`${tendencia} ${unids}/h`}
                         </ValueTendencia>
                    </div>
                    <div>
                         <TitleTendencia>
                              Promedio:
                         </TitleTendencia>
                         <ValueTendencia>
                              {`${prom} ${unids}`}
                         </ValueTendencia>
                    </div>
               </div>
          </Container>
     )

}
