import { Bundle_request } from "../../../../uciTablet/recursosFHIR/helpers/Bundle_request";
import { unitToTwoUnit } from "../../helpers/unitToTwoUnit";
import { extractData } from "./extractData";
import { orderJornada } from "./orderJornada";


export const  getDataEnfermeria = async( id, dayNow, setData ) => {

     const resources = [
          { resource:'Basic', identifier:`ProgramacionEnferIzq`, args:[['ProgramacionEnfer','izq'], 'BD','BI','PD','PI','Trax','NA','NOMBRE: Enfermero Turno Diurno'], daily:true },
          { resource:'Basic', identifier:`ProgramacionEnferDer`, args:[['ProgramacionEnfer','der'], 'BD','BI','PD','PI','Trax','NA','NOMBRE: Enfermero Turno Nocturno'], daily:true }
     ];


     const date = `${dayNow[2]}-${unitToTwoUnit(dayNow[1])}-${unitToTwoUnit(dayNow[0])}` ;
     const array_bundle_disp = await Bundle_request( id, resources, date );

     const tablasProgramacion = {
          resources:[
               { resource: 'Basic', identifier:'ProgramacionEnfer',args:['ProgramacionEnfer'] }
          ],
          fields: [
               'Aseo y Confort','Aseo Matinal','Bao en Cama','Lavado de Pelo','Aseo Genital','Aseo Cavidades','Aseo Ocular',
               'Prevencin UPP','Colchn ant-esc.','Movilizacin','Riesgo Medio(4h)','Riesgo alto(2h)','Medidas Aisl.','Bao Clorex',
               'Aseo Bucal','Procedimientos','Curacin CVC','Cms0','Sitio de insercin','Puntos de fijacin','UPP','Ubicacin','Grado',
               'Instalacin Dispositivos','VVP/LA/SF','Cms1','Cms2','Cms3' //,'Izq','Der'
          ]
     };

     const array_bundle_event = await Bundle_request( id, tablasProgramacion, date );


     // construir objetos de datos :

     const data = {
          ...extractData([ array_bundle_disp[0] ], resources[0].args.slice(1,), ['dia'] ),
          ...extractData([ array_bundle_disp[1] ], resources[1].args.slice(1,), ['noche'] )   
     };

     // Object.assign( dataDisp, extractData([ array_bundle_disp[1] ], resources[1].args.slice(1,), ['noche'] ))

     const dataEvent = {
          ...extractData( array_bundle_event, tablasProgramacion.fields, tablasProgramacion.fields )
     };

     orderJornada( data, dataEvent );
     console.log( data );
     setData ( data );

};