import React, {useEffect} from "react";
import * as d3 from "d3";
import { Box } from '@mui/material';

import { MediaQuery } from '../../MediaQuery';


const Plot = (data, date, svgMed, svgMed2, _xl, _lg) => {

     const color_fill = 'rgba(0, 91, 165, 1)' ;

     const svgEl = d3.select(svgMed.current);
     const svgEl2 = d3.select(svgMed2.current);

     svgEl.selectAll("*").remove(); // Clear svg content before adding new elements
     svgEl2.selectAll("*").remove(); // Clear svg content before adding new elements

     const width = svgEl._groups[0][0].clientWidth  ;
     const height = svgEl._groups[0][0].clientHeight  ;

     const margin = {
          top:30,
          right: _xl?75:_lg?60:45,
          bottom: 1,
          left: 2,
          m:2,
          offsetLeft:_xl?240:_lg?180:130,
          offsetX:_xl?0.1:_lg?0.1:-0.1,
          offsety:_xl?2:_lg?2.5:3,
          offSetMedicamentos:(_xl?175:_lg?140:100),
          heightFila:(_xl?35:_lg?30:24),
          widthMarca:(_xl?45:_lg?40:35),
          py:(_xl?5:3)
     }

     const hours = [];
     const iter = _xl ? 4 : 4 ;
     const fontSize = _xl ? '22px' : _lg ? '16px': '12px' ;
     const fontSizeSub = _xl ? '25px' : _lg ? '20px': '14px' ;
     const fontSizeBod =  _xl ? '22px' : _lg ? '16px': '12px' ;

     for(let k=0; k<=24; k = k + iter ){
          if(k<10){
               hours.push(`0${k}:00`);
          }else{
               hours.push(`${k}:00`);
          }
     } ;

     let limits = d3.extent( hours, (d) => ( d )) ;

     const xScale = d3.scaleLinear()
          .domain( [ parseInt(limits[0]) , parseInt(limits[1]) ] )
                    .range([margin.offsetLeft+margin.offSetMedicamentos, width - margin.right] ) ;

     const yScale = d3.scaleLinear()
                    .domain( [ 0, data.length ] )
                    .range( [margin.top, data.length*margin.heightFila-margin.bottom] ) ;
                    //.range( [margin.top, height-margin.bottom] ) ;


     // Total categorias
     const categorias =  [] ;
     data.forEach( (item,idx) => {
          let rtn = true
          data.slice(idx+1).forEach( item2 =>{
               if( item.categoria === item2.categoria){
                    rtn = false
               }
          })
          if( rtn ) categorias.push( item.categoria ) ;
     });

     //Total de filas por categoria
     const filasMed = [] ;
     categorias.forEach( item =>{
          let count = 0 ;
          data.forEach( item2 => {
               if(item === item2.categoria ) count++
          });
          filasMed.push(count);
     });

     //Medicamentos ordenados por categoria
     const medicamentos = [] ;
     categorias.forEach( (categ) =>{
          data.forEach( (obj) => {
               if(obj.categoria === categ)
               medicamentos.push( obj.medicamento )
          })
     });

     const svg2 = svgEl.append("g")

     //Segunda palabra en segundo renglon
     const subLine = (word,fontWeight,x,y) =>{
          svg2.selectAll(null)
               .data( word )
               .enter()
               .append('text')
               .text( d => d )
               .attr('x', x )
               .attr('y', yScale( y ) )
               .attr('font-family', 'Montserrat')
               .attr('font-size', fontSizeSub )
               .attr('font-weight', fontWeight )
               .attr('fill', color_fill )
               .attr('id', 'secondLine' );
     };

     // cortar palabras, crear abreviacion
     const cut = ( word ) => {
          let words = word.split(' ');
          if( words.length > 1) {
               return words[0]+ ' ' +words[1].slice(0,4) +'.'
          } ;
          return word
     };

     //Categorias
     svg2.selectAll( null )
          .data( categorias )
          .enter()
          .append('text')
          .text( (d,i) => {
               let words = d.split(' ');
               if( words.length > 1) {
                    let y = i + 1 ;
                    if( i !== 0){
                         y =  filasMed.slice(0,i).reduce((a,e) => a+e) + 1
                    };
                    subLine( [words[1]], 'bold',margin.left, y)
                    return words[0]+'-'
               } ;
               return d
          })
          .attr('x', margin.left )
          .attr('y', (d,i) =>
               i === 0
               ? yScale( 0 )
               : yScale( filasMed.slice(0,i).reduce((a,e) => a+e) )
          )
          .attr('font-family', 'Montserrat')
          .attr('font-size', fontSizeSub )
          .attr('font-weight', 'bold')
          .attr('fill', color_fill )
          .on('mousewheel', () => console.log("wheel") );

     //Medicamentos
     svg2.selectAll(null)
          .data( medicamentos )
          .enter()
          .append('text')
          .text( (d,i) => {
               return cut( d )
          })
          .attr('x', margin.offSetMedicamentos )
          .attr('y', (d,i) => yScale( i )  )
          .attr('font-family', 'Montserrat')
          .attr('font-size', fontSizeBod )
          .attr('font-weight', 'normal')
          .attr('fill', color_fill ) ;

     //lineas
     svg2.selectAll(null)
          .data( data )
          .enter()
          .append('line')
          .attr('x1', xScale(0) )
          .attr('y1',(d,i) => yScale(i) - 5 )
          .attr('x2', xScale(24) + margin.widthMarca )
          .attr('y2',(d,i) => yScale(i) - 5 )
          .attr('stroke-width', margin.heightFila - (_xl?10:_lg?9:9) )
          .attr('stroke',"#B5DCE5")
          .attr('stroke-linecap',"round");


     //disparar el wheel del mouse
     // const svg3 = svgEl.append("g")
     //      .attr("width", `${width}`)
     //      .attr("height",`${height}`)
     //      .on("wheel", event => {
     //           event.preventDefault()
     //           zoom( -event.deltaY );
     //      });
     //
     // svg3.append('rect')
     //      .attr('x',0)
     //      .attr('y',0)
     //      .attr('width',width)
     //      .attr('height',height)
     //      .attr('fill','transparent');

     // const gBody = d3.select(`#body`);
     // const maxScroll = gBody._groups[0][0]?.viewportElement.scrollHeight?
     //                     gBody._groups[0][0].viewportElement.scrollHeight:
     //                     margin.heightFila*medicamentos.length ;
     // let posY = 0 ;
     // const zoom = ( delta ) => {
     //      return
     //      if( posY+delta < 100 && Math.abs(posY+delta) < maxScroll ){
     //           posY = posY + delta/5 ;
     //      };
     //      svg2.attr("transform", `translate(0, ${posY})`)
     //      svg4.attr("transform", `translate(0, ${posY})`)  ;
     // };

     // calcular las horas de medicacin
     const arrayHours = [] ;

     data.forEach( ( _, idx )=>{

          let obj =  data.find( d => d.medicamento === medicamentos[idx] ) ;

          let dif = date[0] - obj.date.getDate() + 30*(date[1]-obj.date.getMonth()-1) // dias
          arrayHours.push([]);
          let n = 0;
          let h = 0;

          if( obj.intervaloValue !== 0 & obj.intervaloValue !== 1 & dif >= 0){
               do{
                    h = n*obj.intervaloValue ;
                    if( h  > (dif*24 - 24) & dif >= 0 ){ // calcular solo dia presente
                         arrayHours[idx].push( (h + obj.date.getHours())%24 )
                    }
                    n = n + 1 ;
               }while( h <= dif*24 );
          }else if(  dif >= 0){
               arrayHours[idx].push( obj.intervaloValue === 1
                    ? `${obj.intervaloValue}  ${obj.intervaloUnit.replace(/ /g,'')}`
                    : obj.intervaloUnit.replace(/ /g,'')
               );
          }
     });

     
     // MARCAS
     const svg4 = svgEl.append("g")
                    .attr('id','body');

     arrayHours.forEach( (item,idx) => {
          //Marcas de linea
          svg4.selectAll(null)
               .data( item )
               .enter()
               .append('line')
               .attr('x1', d => isNaN(d)
                         ? xScale( 0 + margin.offsetX )
                         : xScale( d + margin.offsetX )
               )
               .attr('y1', (d,i) => yScale(idx) - 5 )
               .attr('x2', d => isNaN(d)
                         ? xScale( fontSizeSub.split('px')[0]*d.length*0.035 + margin.offsetX )
                         : xScale( d + 2 )
               )
               .attr('y2', (d,i) => yScale(idx) - 5 )
               .attr('stroke-width', margin.heightFila - (_xl?10:_lg?9:9))
               .attr('stroke',"#005BA5")
               .attr('stroke-linecap',"round")

          //  marcas de text
          svg4.selectAll(null)
               .data( item )
               .enter()
               .append('text')
               .text( d => isNaN(d)
                         ? d
                         : d<10 ? `0${d}:00`: `${d}:00`
               )
               .attr('x', d => isNaN(d)
                         ? xScale( d.length*fontSize.split('px')[0]*(_xl?0.009:_lg?0.0055:0.001) + margin.offsetX )
                         : xScale( parseInt(d) + 0.2 + margin.offsetX)
               )
               .attr('y', d => yScale(idx+(_xl?0.18:0.1)) - margin.offsety )
               .attr('font-family', 'Montserrat')
               .attr('font-size', fontSize )
               .attr('font-weight', 'normal')
               .attr('fill', '#FFFFFF' )
               .attr('text-anchor', 'center')

          //Marcas de linea para cuadros emergentes por mouse
          svg4.selectAll(null)
               .data( item )
               .enter()
               .append('line')
               .attr('x1', d => isNaN(d)
                         ? xScale( 0 + margin.offsetX )
                         : xScale( d + margin.offsetX )
               )
               .attr('y1', (d,i) => yScale(idx) - 5 )
               .attr('x2', d => isNaN(d)
                         ? xScale( d.length*fontSize.split('px')[0]*(_xl?0.04:_lg?0.04:0.05) + margin.offsetX )
                         : xScale( d +2 )
               )
               .attr('y2', (d,i) => yScale(idx) - 5 )
               .attr('stroke-width', margin.heightFila - (_xl?10:_lg?9:9))
               .attr('stroke',"transparent")
               .attr('stroke-linecap',"round")
               .on('mouseover', (e) => mouseOn(e,idx))
               .on('mouseout',() => {
                    cuadro.style("display", 'none')
                    textCuadro.style("display", 'none')
               })
               // .on("wheel", event => {
               //      event.preventDefault()
               //      zoom( -event.deltaY );
               // });
     });


     //Cuadros emergentes
     const cuadro = svg4.append('g')
          .append("rect")
          .attr("height", parseInt(fontSizeSub.split('px')[0])*(_xl?3.5:4)  )
          .attr("width", 50)
          .attr("fill","#005BA5")
          .attr('rx',8)
          .attr('ry',8)
          .attr('stroke', 'cadetblue')
          .attr('stroke-width', 2)
          .style("display", 'none');

     const textCuadro = svg4.append('g')
          .selectAll(null)
          .data( new Array(3) )
          .enter()
          .append('text')
          .attr('font-family', 'Montserrat')
          .attr('font-size', fontSizeSub )
          .attr('font-weight', 'normal')
          .attr('fill', '#FFFFFF' )
          .attr('align','center')
          .style("display", 'block');
          

     const  mouseOn = (e,i) =>{

          let x = parseInt(e?.originalTarget?.attributes?.x1?.nodeValue) || parseInt(e?.toElement?.attributes?.x1?.value) ;
          let y = parseInt(e?.originalTarget?.attributes?.y1?.nodeValue) || parseInt(e?.toElement?.attributes?.y1?.value) ;

          const dataText = [] ;
          const dataMed = data.find( d => d.medicamento === medicamentos[i] ) ;
          dataText.push( dataMed.medicamento ) ;
          dataText.push( (dataMed.intervaloValue === 0 || dataMed.intervaloValue === 1)
                         ? dataMed.intervaloValue === 1
                              ? `${dataMed.intervaloValue} ${dataMed.intervaloUnit.replace(/ /g,'')}`
                              : dataMed.intervaloUnit.replace(/ /g,'')
                         : `Cada ${dataMed.intervaloValue} ${dataMed.intervaloUnit.replace(/ /g,'')}`
                    ) ;
          dataText.push( `Dosis de ${dataMed.dosisValue} ${dataMed.dosisUnit}` ) ;

          let lenMax = 0 ;
          dataText.forEach( (item) => lenMax = item.length > lenMax ? item.length : lenMax );
          lenMax = parseInt(fontSizeSub.split('px')[0])*lenMax*0.62

          cuadro
               .attr( 'x', x - lenMax - 5)
               .attr( 'y', yScale(i) - 30)
               .attr( "width", lenMax )
               .style( "display", 'block') ;

          textCuadro
               .text( (_,i) => dataText[i] )
               .attr( 'x', x - lenMax - 1 )
               .attr( 'y', (_,idx) =>  idx*margin.heightFila*0.6 - yScale(i) + y*2 ) //- yScale(i)*1.1 ) )
               .style( "display", 'block');
     };

     //Eje x de horas
     const svgh = svgEl2.append("g")


     //fondo del eje x
     svgh.append('rect')
          .attr('x', margin.rigth   )
          .attr('y',20 ) //height - margin.heightFila )
          .attr("width", `${width}`)
          .attr("height",`${height}`)
          .attr('fill','white');

     //eje x
     svgh.selectAll(null)
          .data( hours )
          .enter()
          .append('text')
          .text( (d,i) => (d) )
          .attr('x',(d,i) => xScale(i*iter) )
          .attr('y',20 ) //height - margin.bottom )
          .attr('font-family', 'Montserrat')
          .attr('font-size', fontSize )
          .attr('fill', color_fill ) ;

};

export const PlotMedicacion = ( { data, date } ) => {

     const { _xl, _lg } = MediaQuery();

     const svgMed = React.useRef(null);
     const svgMed2 = React.useRef(null);

     // eslint-disable-next-line no-use-before-define
     useEffect( () => {
          Plot(data,date,svgMed,svgMed2, _xl, _lg)
     },[data,date,svgMed,svgMed2,_xl, _lg]) ;

     return(
          <div>
               <Box
                    style={{maxHeight: _xl?'180px': _lg?'175px':'110px', overflowY: 'scroll', scrollBehavior: 'smooth'}}
               >
                    <svg ref={svgMed} width={'100%'} height={data.length*(_xl?35:_lg?30:25)} />
               </Box>
               <svg ref={svgMed2} width={'100%'} height={'30'} />
          </div>
     );
};
