import React from 'react';
import { Box } from '@mui/material';
import { PlotSindromatico } from './PlotSindromatico';

import { SubTitle } from '../elements/SubTitle'

export const DashContentSindro = ({stl, data}) => {

     const classes = stl ;

     const style_title = [classes.bg_blue_sindromatico, classes.px_0].join(' ') ;

      return(
           <Box>

               <Box
                    className={style_title}
               >
                    <SubTitle color={'white'}>
                        {data.title} Clnico
                    </SubTitle>
               </Box>
               <Box className={classes.py}>
                    <PlotSindromatico
                         data={data.clinico}
                         n={data.clinico.length}
                    ></PlotSindromatico>
               </Box>

               <Box
                    className={style_title}
               >
                    <SubTitle color={'white'}>
                        {data.title} Laboratorio
                    </SubTitle>
               </Box>
               <Box className={classes.py}>
                    <PlotSindromatico
                         data={data.laboratorio}
                         n={data.laboratorio.length}
                    ></PlotSindromatico>
               </Box>

           </Box>
      );
}
