
import { unitToTwoUnit } from '../../helpers/unitToTwoUnit' ;
import { resourceServer } from '../../../env' ;
import { to24hours }  from './to24hours' ;
import { prom } from './prom' ;
import { dataFormat, variables } from './dataFormat' ;
import { variablesControlHemo } from './variablesControlHemo'  ;

// enviroment
import { addToken } from '../../helpers/token'; 
const { urlServer, requestOptionsPost } = resourceServer ;

export const getInfo = async ( id, dayNow, setVitalSings ) => {

     const dateFromTimeLine = `${dayNow[2]}-${unitToTwoUnit(dayNow[1])}-${unitToTwoUnit(dayNow[0])}` ;
     // const dateFromTimeLineTom = `${dayNow[2]}-${unitToTwoUnit(dayNow[1])}-${unitToTwoUnit(parseInt(dayNow[0])+1)}` ;

     const bundle = {
          resourceType : "Bundle",
          type : "batch",
          entry:[]
     };

     const vitalSings = {
          PresArterial: dataFormat(variables).PresArterial,
          FrecCardiaca: dataFormat(variables).FrecCardiaca,
          TempCorporal: dataFormat(variables).TempCorporal,
          FrecRespiratoria: dataFormat(variables).FrecRespiratoria,
     } ;

     const labels = [];
     ['alarmaDia:','alarmaNoche:'].forEach( jornada => {
          [
               'Hora', 'P/A','FC','TC','FR',
               'Cdigo','PAM','PVC','PAP','PCP','ngulo de Cabecera','EVA/S.A.S'
               ,'Cd./Modo','FIO2','IPAP/PS','EPAP/PEEP','PC','VOL.CTE','VOL.MIN','SAT','CUFF/CMS',
               'DU Parcial','DU Total','Deposiciones','HGT','Insulina','Residuo Gstrico','Evaluacin contenciones',
          ].forEach( item => labels.push( jornada+item) )
     }) ;

     labels.forEach( label =>{
          bundle.entry.push(
               {
                    "request" : {
                         "method" : "GET",
                         "url" : `Basic?subject=${id}&identifier=${label.replace(/[^a-zA-Z0-9:]/g,'')}&identifier=${dateFromTimeLine}T00:00:00Z`
                    }
               }
          )
     })

     requestOptionsPost.body =  JSON.stringify( bundle )

     const config = addToken(requestOptionsPost) ;
     config.headers.append("Content-Type", "application/json") ;

     const respBundle = await fetch(`${urlServer}`, config );
     const objBundle = await respBundle.json();

     const resources = [];
     objBundle.entry.forEach( entry => {
          if( entry.resource.total > 0){
               resources.push( entry.resource.entry[0].resource )
          }
     });

     if( resources.length > 0 ){

          const paramJornada = [] ;

          ['alarmaDia','alarmaNoche'].forEach( jornada => { // Parametros por jornada
               paramJornada.push(
                    resources.filter( rsc => rsc.code.text.indexOf( jornada ) >= 0 )
                         .map( rsc => rsc.code )
               )
          }) ;

          let parametros = paramJornada.map( arrayPar =>
               arrayPar.map( (obj,idx,arr) => {
                    return({
                         name: obj.text.split(':')[1],
                         data: obj.coding.reduce( (acc,act)=>[
                              ...acc,
                              idx
                              ? !parseFloat(act.display)
                                   ? prom(acc)  // Valor promedio para reemplazar un NaN
                                   : parseFloat(act.display)
                              : to24hours( act.display )
                         ],[])
                    })
               })
          ) ;

          // agrupar datos de las jornadas
          parametros = parametros[ parametros.reduce( (acc,act,idx)=> acc = act.length > acc ? idx : acc , 0 ) ]
                         .map( p => {
                              let dataDia = parametros[0].find( p2 => p2.name === p.name)?.data
                                   ? parametros[0].find( p2 => p2.name === p.name).data
                                   : [ '0', '0' ];

                              let dataNoche = parametros[1].find( p2 => p2.name === p.name)?.data
                                   ? parametros[1].find( p2 => p2.name === p.name).data
                                   : [ '0', '0' ];

                              return({
                                   name: p.name,
                                   data: [...dataDia, ...dataNoche]
                              })
                         }) ;

          Object.keys( vitalSings ).forEach( label =>
               vitalSings[label] = {
                    ...vitalSings[label],
                    array: parametros.length > 0
                              ? parametros.find( p => p.name === 'Hora' ).data
                                   .map( (hour,idx) => [
                                        parametros.find( p => p.name === vitalSings[label].key )
                                             ? parametros.find( p => p.name === vitalSings[label].key ).data[idx]
                                                  ? parametros.find( p => p.name === vitalSings[label].key ).data[idx]
                                                  : '0'
                                             : '0' ,
                                        hour
                                   ])
                              : [['0','0']]
               }
          ) ;

          parametros.forEach( obj => {
               vitalSings[obj.name] = obj.data
          });

          // Carga de parametros sindromaticos
          variablesControlHemo.forEach( v => {
               variables[v[1]].value = prom( vitalSings[v[0]] ).toFixed(1)
          })

        //  console.log( Object.assign( dataFormat( variables ), vitalSings ) );
     };

     setVitalSings({
          ...Object.assign( dataFormat( variables ), vitalSings )
     });
};
